import * as React from 'react';
import { Link } from 'react-router-dom';
import { Spinner } from '../common';
import autobind from 'autobind-decorator';

import { BrightcoveApiGateway, BCExperience } from '@brightcove/video-cloud-sdk';
import { SharePointApiGateway } from '../../services/sharepoint';
import { ConnectorContext } from '../../services/connector';

import './PreviewExperience.css';

export class PreviewExperience extends React.Component<PreviewExperienceProps, PreviewExperienceState> {
  constructor(props: PreviewExperienceProps, context?: any) {
    super(props, context);

    this.state = {
      isLoadingExperience: true
    };
  }

  @autobind
  async componentDidMount() {
    await this.getExperience();
  }

  @autobind
  async getExperience() {
    const { accountId, experienceId, bcApiGateway } = this.props;

    if (!experienceId) {
      window.location.hash = `#/${accountId}/manage-experiences`;
      return;
    }

    this.setState({
      isLoadingExperience: true
    });

    const experiencesApi = bcApiGateway.experiencesApi;
    const bcExperience = await experiencesApi.getExperienceById(accountId, experienceId);

    this.setState({
      isLoadingExperience: false,
      experience: bcExperience
    });
  }

  render() {
    const { experienceId, accountId } = this.props;
    const { isLoadingExperience, experience } = this.state;

    if (isLoadingExperience || !experience) {
      return <Spinner loadingText="Loading Experience..." />;
    }

    return (
      <>
        <ol className="breadcrumb">
          <li>
            <Link to="/">Brightcove Management</Link>
          </li>
          <li>
            <Link to={`/${accountId}/manage-experiences`}>Manage Experiences</Link>
          </li>
          <li>
            <Link to={`/${accountId}/manage-experiences`}>Account {accountId}</Link>
          </li>
          <li className="active">Experience {experienceId.substr(0, 7)}</li>
        </ol>
        <div className="form-horizontal">
          <h3>{experience.name}</h3>
          <div className="row">
            <iframe title="Experience" className="col-md-12" src={experience.previewUrl} style={{ height: '500px' }} />
          </div>
          <hr />
          <div className="pull-right">
            <Link className="btn btn-default btn-space" to={`/${experience.accountId}/manage-experiences`}>
              Cancel
            </Link>
          </div>
        </div>
      </>
    );
  }
}

interface PreviewExperienceProps {
  connectorContext: ConnectorContext;
  bcApiGateway: BrightcoveApiGateway;
  spApiGateway: SharePointApiGateway;
  accountId: string;
  experienceId: string;
}

interface PreviewExperienceState {
  isLoadingExperience: boolean;
  experience?: BCExperience;
}
