import * as React from 'react';
import { Link } from 'react-router-dom';
import { Button, Modal } from 'react-bootstrap';
import { BootstrapTable, TableHeaderColumn } from 'react-bootstrap-table';
import { VideoEmbed, EmbedLayoutMode } from '../embeds';
import { VideoSearchControls } from './VideoSearchControls';
import { AccountPicker, Spinner } from '../common';
import autobind from 'autobind-decorator';

import { BrightcoveAccount } from 'services';
import { ConnectorContext, ConnectorVideo } from '../../services/connector';
import { SearchTarget, VideoSearchParameters, getDefaultVideoSearchParameters } from '../../services/connector';

import { find, filter, startsWith } from 'lodash';

import './ManageVideos.css';
import { BrightcovePlayerProps, BrightcovePlayerUtils } from '../brightcove-player';

export class ManageVideos extends React.Component<ManageVideosProps, ManageVideosState> {
  constructor(props: ManageVideosProps, context?: any) {
    super(props, context);

    this.state = {
      searchParameters: getDefaultVideoSearchParameters(),
      isLoadingVideos: false,
      showEmbedModal: false,
      defaultVideoPlayerId: ''
    };
  }

  @autobind
  async UNSAFE_componentWillMount() {
    const { accountId, connectorContext } = this.props;

    const selectedAccount = find(connectorContext.availableAccounts, account => {
      return account.accountId === accountId;
    });
    if (selectedAccount) {
      this.setState({
        defaultVideoPlayerId: selectedAccount.defaultVideoPlayerId
      });
    }

    this.searchForVideos(this.props.accountId, this.state.searchParameters);
  }

  @autobind
  async UNSAFE_componentWillUpdate(nextProps: ManageVideosProps) {
    if (nextProps.accountId !== this.props.accountId) {
      this.searchForVideos(nextProps.accountId, this.state.searchParameters);
    }
  }

  @autobind
  async searchForVideos(accountId: string, searchParams: Partial<VideoSearchParameters>): Promise<void> {
    const { connectorContext } = this.props;

    if (!accountId) {
      return;
    }

    this.setState({
      isLoadingVideos: true
    });

    let videos: Array<ConnectorVideo>;
    switch (searchParams.searchTarget) {
      case SearchTarget.VideoCloud:
        videos = await connectorContext.searchForBrightcoveVideos(accountId, searchParams);
        break;
      default:
        return;
    }

    this.setState({
      isLoadingVideos: false,
      videos
    });
  }

  @autobind
  handleAccountChange(newAccount: BrightcoveAccount) {
    const { accountId, onAccountChange } = this.props;

    if (accountId !== newAccount.accountId) {
      if (onAccountChange) {
        onAccountChange(newAccount.accountId);
      }
    }
  }

  @autobind
  handleEmbedPlayerPropsChanged(embedPlayerProps: Partial<BrightcovePlayerProps>) {
    this.setState({
      embedPlayerProps
    });
  }

  @autobind
  handleSearchParametersChanged(searchParameters: Partial<VideoSearchParameters>, fieldChanged?: string) {
    this.setState({
      searchParameters
    });

    if (fieldChanged !== 'searchTerms') {
      this.searchForVideos(this.props.accountId, searchParameters);
    }
  }

  @autobind
  showEmbedModal(videoId: string, videoTitle: string) {
    const { accountId } = this.props;

    const embedPlayerProps = BrightcovePlayerUtils.getDefaultBrightcovePlayerProps();
    embedPlayerProps.mode = 'Video';
    embedPlayerProps.accountId = accountId;
    embedPlayerProps.playerId = this.state.defaultVideoPlayerId;
    embedPlayerProps.modeId = videoId;
    embedPlayerProps.sharepointVersion = this.props.connectorContext.spAddinContext.SPProductNumber;

    this.setState({
      showEmbedModal: true,
      embedVideoTitle: videoTitle,
      embedPlayerProps
    });
  }

  @autobind
  hideEmbedModal() {
    this.setState({
      showEmbedModal: false,
      embedVideoTitle: undefined,
      embedPlayerProps: undefined
    });
  }

  @autobind
  renderVideoCell(cell: any, video: ConnectorVideo): React.ReactElement<any> {
    const { accountId } = this.props;

    const editVideoUrl = `/${accountId}/manage-videos/${video.videoId}/edit`;
    const tagsToDisplay = filter(video.tags, tag => !startsWith(tag, 'bcsp-'));

    return (
      <div className="result">
        <img className="resultImage col-md-2" src={video.thumbnailUrl} alt="" />
        <div className="col-md-7">
          <div>
            <Link to={editVideoUrl}>{video.name}</Link>
          </div>
          {video.shortDescription && <div className="short-description">{video.shortDescription}</div>}
          {!video.complete && (
            <div className="form-group">
              <div className="alert alert-warning small" style={{ padding: '5px' }} role="alert">
                This video is still processing on the Video Cloud. Renditions may not be available. This might also be a Live event with no thumbnail image.
              </div>
            </div>
          )}
          <div>
            {tagsToDisplay.map((tag, i) => (
              <Button key={i} bsSize="xs" bsStyle="success">
                {tag}
              </Button>
            ))}
          </div>
        </div>
        <div className="col-md-3 text-center">
          <div>
            <Link to={editVideoUrl}>Edit Video</Link>
          </div>
          <div className="video-id">Video Id: {video.videoId}</div>
          <div>
            <Button bsStyle="success" bsSize="small" style={{ width: '90px' }} onClick={() => this.showEmbedModal(video.videoId, video.name)}>
              Embed
            </Button>
          </div>
        </div>
      </div>
    );
  }

  render() {
    const { accountId, connectorContext } = this.props;

    const { isLoadingVideos, searchParameters, videos, defaultVideoPlayerId, showEmbedModal, embedVideoTitle, embedPlayerProps } = this.state;

    const labelClass = 'col-md-3 control-label';
    const controlClass = 'col-md-9';

    return (
      <>
        <ol className="breadcrumb">
          <li>
            <Link to="/">Brightcove Management</Link>
          </li>
          <li className="active">Manage Videos</li>
        </ol>
        <div className="search form-horizontal">
          <div className="form-group">
            <label className={labelClass}>Select Account:</label>
            <div className={controlClass}>
              <AccountPicker availableAccounts={connectorContext.availableAccounts} selectedAccountId={accountId} onChange={this.handleAccountChange} />
            </div>
          </div>
        </div>
        <div className="search form-horizontal">
          <VideoSearchControls
            accountId={accountId}
            connectorContext={connectorContext}
            showSearchTarget={false}
            labelClass={labelClass}
            controlClass={controlClass}
            searchParameters={searchParameters}
            onSearchParametersChanged={this.handleSearchParametersChanged}
            onSearchForVideos={this.searchForVideos}
          />
          {isLoadingVideos && <Spinner loadingText="Loading Videos..." />}
          {!isLoadingVideos && videos && (
            <BootstrapTable data={videos} striped={true} bordered={false} pagination={videos.length > 10} options={{ paginationPosition: 'bottom' }}>
              <TableHeaderColumn dataField="videoId" isKey={true} dataFormat={this.renderVideoCell}>
                Results
              </TableHeaderColumn>
            </BootstrapTable>
          )}
          <Modal className="embed" show={showEmbedModal} onHide={this.hideEmbedModal} bsSize="large">
            <Modal.Header closeButton={true}>
              <Modal.Title>Embed Video</Modal.Title>
            </Modal.Header>
            <Modal.Body>
              {embedPlayerProps && (
                <VideoEmbed
                  layoutMode={EmbedLayoutMode.Full}
                  bcPlayerProps={embedPlayerProps}
                  videoTitle={embedVideoTitle || ''}
                  defaultVideoPlayerId={defaultVideoPlayerId}
                  doesEmbedHelperExist={connectorContext.doesEmbedHelperExist}
                  spAddinContext={connectorContext.spAddinContext}
                  spApiGateway={connectorContext.spApiGateway}
                  showUploadWebPartButton={false}
                  retrieveAllPlayers={connectorContext.retrieveAllPlayers}
                  onPlayerPropsChanged={this.handleEmbedPlayerPropsChanged}
                />
              )}
            </Modal.Body>
            <Modal.Footer />
          </Modal>
        </div>
      </>
    );
  }
}

interface ManageVideosProps {
  accountId: string;
  connectorContext: ConnectorContext;
  onAccountChange?: (accountId: string) => void;
}

interface ManageVideosState {
  searchParameters: Partial<VideoSearchParameters>;

  isLoadingVideos: boolean;
  videos?: Array<ConnectorVideo>;
  defaultVideoPlayerId: string;

  showEmbedModal: boolean;
  embedVideoTitle?: string;
  embedPlayerProps?: Partial<BrightcovePlayerProps>;
}
