import * as React from 'react';
import { Button } from 'react-bootstrap';
import { Spinner } from '../common';
import autobind from 'autobind-decorator';

import { BCVideoTextTrackKind, BCVideoTextTrack } from '@brightcove/video-cloud-sdk';
import { ConnectorVideo } from '../../services/connector';

import { cloneDeep, isArrayBuffer } from 'lodash';

import { ActiveTextTrack, TextTrackAddMode, textTrackLanguages } from './TextTrackTypes';

const defaultTextTrack: ActiveTextTrack = {
  src: '',
  srclang: 'en-US',
  label: '',
  kind: BCVideoTextTrackKind.Captions,
  // eslint-disable-next-line @typescript-eslint/camelcase
  mime_type: 'text/vtt'
};

const urlRegex = /((([A-Za-z]{3,9}:(?:\/\/)?)(?:[-;:&=+$,\w]+@)?[A-Za-z0-9.-]+|(?:www\.|[-;:&=+$,\w]+@)[A-Za-z0-9.-]+)((?:\/[+~%/.\w\-_]*)?\??(?:[-+=&;%@.\w_]*)#?(?:[.!/\\\w]*))?)/;

export class EditVideoTextTracks extends React.Component<EditTextTrackProps, EditTextTrackState> {
  constructor(props: EditTextTrackProps, context?: any) {
    super(props, context);

    this.state = {
      activeTextTrack: cloneDeep(defaultTextTrack),
      textTrackAddMode: TextTrackAddMode.FileUpload,
      isFetchingUrlUpload: false,
      uploadUrlResultText: ''
    };
  }

  @autobind
  async fetchUrlUpload() {
    const { uploadUrl, activeTextTrack } = this.state;

    if (!uploadUrl) {
      return;
    }

    this.setState({
      isFetchingUrlUpload: true
    });

    let resultText = 'File Retrieved';

    try {
      const data = await fetch(uploadUrl, {
        method: 'GET',
        mode: 'no-cors',
        cache: 'no-cache',
        credentials: 'same-origin'
      });

      activeTextTrack.file = await data.arrayBuffer();
    } catch (ex) {
      resultText = `An Error occurred: ${ex}`;
    }

    this.setState({
      isFetchingUrlUpload: false,
      uploadUrlResultText: resultText
    });
  }

  @autobind
  validateAddTextTrack(): boolean {
    const { textTrackAddMode, activeTextTrack } = this.state;

    switch (textTrackAddMode) {
      case TextTrackAddMode.None:
        // Do nothing.
        break;
      case TextTrackAddMode.FileUpload:
      case TextTrackAddMode.UrlUpload:
        if (!activeTextTrack.file) {
          return false;
        }
        break;
      case TextTrackAddMode.RemoteUrl:
        if (!activeTextTrack.src) {
          return false;
        }

        if (!activeTextTrack.src.match(urlRegex)) {
          return false;
        }

        break;
      default:
        return false;
    }

    return true;
  }

  @autobind
  validateUrlUpload(): boolean {
    const { uploadUrl } = this.state;
    if (uploadUrl && uploadUrl.match(urlRegex)) {
      return true;
    }
    return false;
  }

  @autobind
  handleTextTrackAddModeChange(event: React.ChangeEvent<HTMLInputElement>) {
    const { activeTextTrack } = this.state;
    const newAddMode = event.target.value as TextTrackAddMode;
    activeTextTrack.src = '';
    activeTextTrack.file = undefined;

    this.setState({
      textTrackAddMode: newAddMode,
      activeTextTrack,
      isFetchingUrlUpload: false,
      uploadUrl: '',
      uploadUrlResultText: ''
    });
  }

  @autobind
  handleFileUpload(event: React.ChangeEvent<HTMLInputElement>) {
    if (event.target.files) {
      const reader = new FileReader();
      const { activeTextTrack } = this.state;
      // eslint-disable-next-line @typescript-eslint/no-this-alias
      const that = this;
      reader.onload = function() {
        const arrayBuffer = this.result;
        if (!isArrayBuffer(arrayBuffer)) {
          return;
        }
        activeTextTrack.file = arrayBuffer;
        that.setState({
          activeTextTrack
        });
      };

      reader.readAsArrayBuffer(event.target.files[0]);
      event.target.value = '';
    }
  }

  @autobind
  handleUploadUrlChange(event: React.ChangeEvent<HTMLInputElement>) {
    const uploadUrl = event.target.value;

    this.setState({
      uploadUrl
    });
  }

  @autobind
  handleSrcChange(event: React.ChangeEvent<HTMLInputElement>) {
    const { activeTextTrack } = this.state;

    activeTextTrack.src = event.target.value;

    this.setState({
      activeTextTrack
    });
  }

  @autobind
  handleSrcLangChange(event: React.ChangeEvent<HTMLSelectElement>) {
    const { activeTextTrack } = this.state;

    activeTextTrack.srclang = event.target.value;

    this.setState({
      activeTextTrack
    });
  }

  @autobind
  handleLabelChange(event: React.ChangeEvent<HTMLInputElement>) {
    const { activeTextTrack } = this.state;

    activeTextTrack.label = event.target.value;

    this.setState({
      activeTextTrack
    });
  }

  @autobind
  handleKindChange(event: React.ChangeEvent<HTMLSelectElement>) {
    const { activeTextTrack } = this.state;

    activeTextTrack.kind = event.target.value;

    this.setState({
      activeTextTrack
    });
  }

  @autobind
  handleAddTextTrack() {
    const { onAddTextTrack } = this.props;
    const { activeTextTrack } = this.state;

    if (onAddTextTrack) {
      onAddTextTrack(activeTextTrack);
    }

    this.setState({
      activeTextTrack: cloneDeep(defaultTextTrack),
      isFetchingUrlUpload: false,
      uploadUrl: '',
      uploadUrlResultText: ''
    });
  }

  @autobind
  handleRemoveTextTrack(textTrack: Partial<BCVideoTextTrack>) {
    const { onRemoveTextTrack } = this.props;
    if (onRemoveTextTrack) {
      onRemoveTextTrack(textTrack);
    }
  }

  @autobind
  handleDefaultTextTrackChange(textTrack: Partial<BCVideoTextTrack>, event: React.ChangeEvent<HTMLInputElement>) {
    const { onDefaultTextTrackChanged } = this.props;
    if (onDefaultTextTrackChanged) {
      onDefaultTextTrackChanged(textTrack, event.target.checked);
    }
  }

  render() {
    const { video } = this.props;
    const { textTrackAddMode, activeTextTrack, isFetchingUrlUpload, uploadUrlResultText } = this.state;

    const canAddTextTrack = this.validateAddTextTrack();
    const canUrlUpload = this.validateUrlUpload();

    return (
      <>
        <h4>
          <strong>Text Track Fields</strong>
        </h4>
        <hr />
        <div className="form-horizontal">
          <div className="form-group col-md-12">
            <input
              className="radio-inline"
              type="radio"
              name="texttrackfiletype"
              value={TextTrackAddMode.None}
              checked={textTrackAddMode === TextTrackAddMode.None}
              onChange={this.handleTextTrackAddModeChange}
            />{' '}
            None
            <input
              className="radio-inline"
              type="radio"
              name="texttrackfiletype"
              value={TextTrackAddMode.FileUpload}
              checked={textTrackAddMode === TextTrackAddMode.FileUpload}
              onChange={this.handleTextTrackAddModeChange}
            />{' '}
            File Upload
            <input
              className="radio-inline"
              type="radio"
              name="texttrackfiletype"
              value={TextTrackAddMode.UrlUpload}
              checked={textTrackAddMode === TextTrackAddMode.UrlUpload}
              onChange={this.handleTextTrackAddModeChange}
            />{' '}
            Url Upload
            <input
              className="radio-inline"
              type="radio"
              name="texttrackfiletype"
              value={TextTrackAddMode.RemoteUrl}
              checked={textTrackAddMode === TextTrackAddMode.RemoteUrl}
              onChange={this.handleTextTrackAddModeChange}
            />{' '}
            Remote Url
          </div>
          {textTrackAddMode === TextTrackAddMode.None && <div />}
          {textTrackAddMode === TextTrackAddMode.FileUpload && (
            <div className="form-group col-md-12">
              <input type="file" accept=".vtt" onChange={this.handleFileUpload} />
              {activeTextTrack && activeTextTrack.file && <div style={{ fontSize: 'smaller' }}>File Uploaded</div>}
            </div>
          )}
          {textTrackAddMode === TextTrackAddMode.UrlUpload && (
            <div className="form-group col-md-12 row">
              {isFetchingUrlUpload && <Spinner loadingText="Retrieving Remote Text Track" />}
              {!isFetchingUrlUpload && (
                <div className="col-md-10">
                  <input className="form-control" placeholder="Add URL" onChange={this.handleUploadUrlChange} />
                  {activeTextTrack && activeTextTrack.file && <div style={{ fontSize: 'smaller' }}>{uploadUrlResultText}</div>}
                </div>
              )}
              <div className="col-md-2">
                <Button bsStyle={canUrlUpload ? 'primary' : 'default'} disabled={!canUrlUpload} onClick={this.fetchUrlUpload}>
                  Upload
                </Button>
              </div>
            </div>
          )}
          {textTrackAddMode === TextTrackAddMode.RemoteUrl && (
            <div className="form-group col-md-12">
              <input className="form-control" type="url" placeholder="Add URL" value={activeTextTrack.src} onChange={this.handleSrcChange} />
            </div>
          )}

          <table className="table table-striped">
            <thead>
              <tr>
                <td>
                  <label className="required">Language</label>
                </td>
                <td>
                  <label>Label</label>
                </td>
                <td>
                  <label className="">Kind</label>
                </td>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>
                  <select className="form-control" value={activeTextTrack.srclang} onChange={this.handleSrcLangChange}>
                    {textTrackLanguages.map((language, i) => (
                      <option key={i}>{language}</option>
                    ))}
                  </select>
                </td>
                <td>
                  <input className="form-control" type="text" value={activeTextTrack.label} onChange={this.handleLabelChange} />
                </td>
                <td>
                  <select className="form-control" value={activeTextTrack.kind} onChange={this.handleKindChange}>
                    <option>captions</option>
                    <option>subtitles</option>
                    <option>descriptions</option>
                    <option>chapters</option>
                    <option>metadata</option>
                  </select>
                </td>
              </tr>
            </tbody>
            <tfoot>
              <tr>
                <td>
                  <Button bsStyle={canAddTextTrack ? 'primary' : 'default'} onClick={this.handleAddTextTrack} disabled={!canAddTextTrack}>
                    Add
                  </Button>
                </td>
              </tr>
            </tfoot>
          </table>

          {video.textTracks && (
            <table className="table table-striped">
              <thead>
                <tr>
                  <td>Label</td>
                  <td>Language</td>
                  <td>Kind</td>
                  <td>Default</td>
                  <td style={{ width: '100px' }} />
                </tr>
              </thead>
              <tbody>
                {video.textTracks.map((textTrack, ix) => (
                  <tr key={ix}>
                    <td>{textTrack.label}</td>
                    <td>{textTrack.srclang}</td>
                    <td>{textTrack.kind}</td>
                    <td className="text-center" style={{ width: '50px' }}>
                      <div className="checkbox">
                        <label>
                          <input type="checkbox" checked={textTrack.default === true} onChange={e => this.handleDefaultTextTrackChange(textTrack, e)} />
                        </label>
                      </div>
                    </td>
                    <td>
                      <Button bsStyle="link" onClick={() => this.handleRemoveTextTrack(textTrack)}>
                        Remove
                      </Button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          )}
        </div>
      </>
    );
  }
}

interface EditTextTrackProps {
  video: ConnectorVideo;
  onAddTextTrack?: (textTrack: ActiveTextTrack) => void;
  onRemoveTextTrack?: (textTrack: Partial<BCVideoTextTrack>) => void;
  onDefaultTextTrackChanged?: (textTrack: Partial<BCVideoTextTrack>, isChecked: boolean) => void;
}

interface EditTextTrackState {
  textTrackAddMode: TextTrackAddMode;
  activeTextTrack: ActiveTextTrack;

  uploadUrl?: string;
  isFetchingUrlUpload: boolean;
  uploadUrlResultText: string;
}
