import * as React from 'react';
import { useState, useEffect } from 'react';
import { FormControl } from 'react-bootstrap';

import { BrightcoveAccount } from 'services';

import { find } from 'lodash';

export const AccountPicker = (props: AccountPickerProps) => {
  const { availableAccounts, selectedAccountId, onChange, disabled, className } = props;
  const [isLoadingAccounts, setIsLoadingAccounts] = useState(true);

  useEffect(() => {
    setIsLoadingAccounts(true);
    if (!selectedAccountId && availableAccounts && availableAccounts.length > 0) {
      const newSelectedAccount = availableAccounts[0];
      if (onChange) {
        onChange(newSelectedAccount);
      }
    }

    setIsLoadingAccounts(false);
  }, [isLoadingAccounts]);

  const handleFieldChange = (event: React.FormEvent<FormControl>) => {
    const target = event.target as any;

    const selectedAccount = find(availableAccounts, account => {
      return account.accountId === target.value;
    });

    if (selectedAccount && onChange) {
      onChange(selectedAccount);
    }
  };

  if (isLoadingAccounts) {
    return (
      <FormControl className={className} componentClass="select" disabled={true}>
        <option>Loading Accounts...</option>
      </FormControl>
    );
  }

  if (!availableAccounts || availableAccounts.length <= 0) {
    return (
      <FormControl className={className} componentClass="select" disabled={true}>
        <option>There are no available accounts.</option>
      </FormControl>
    );
  }

  return (
    <FormControl className={className} componentClass="select" placeholder="Select an account..." value={selectedAccountId} onChange={handleFieldChange} disabled={disabled || false}>
      {availableAccounts.map((account, i) => (
        <option key={i} value={account.accountId}>
          {account.title}
        </option>
      ))}
    </FormControl>
  );
};

interface AccountPickerProps {
  className?: string;
  availableAccounts: Array<BrightcoveAccount>;
  selectedAccountId?: string;
  onChange?: (selectedAccount: BrightcoveAccount) => void;
  disabled?: boolean;
}
