import * as React from 'react';
import { Button } from 'react-bootstrap';
import autobind from 'autobind-decorator';

import { BCFolder } from '@brightcove/video-cloud-sdk';
import { ConnectorContext, SearchTarget, VideoSortField, SortOrder, VideoSearchParameters } from '../../services/connector';
import { FolderPicker } from '../common';

export class VideoSearchControls extends React.Component<VideoSearchControlsProps> {
  @autobind
  async handleSearchTermsChange(e: React.ChangeEvent<HTMLInputElement>) {
    const { searchParameters, onSearchParametersChanged } = this.props;
    searchParameters.searchTerms = e.target.value;

    onSearchParametersChanged(searchParameters, 'searchTerms');
  }

  @autobind
  handleSearchTargetChanged(newSearchTarget: SearchTarget) {
    const { searchParameters, onSearchParametersChanged } = this.props;
    searchParameters.searchTarget = newSearchTarget;

    onSearchParametersChanged(searchParameters);
  }

  @autobind
  handleSelectedFolderChange(newSelectedFolder?: BCFolder) {
    const { searchParameters, onSearchParametersChanged } = this.props;
    if (newSelectedFolder) {
      searchParameters.folderId = newSelectedFolder.id;
    } else {
      searchParameters.folderId = '';
    }

    onSearchParametersChanged(searchParameters);
  }

  @autobind
  handleSearchLimitChange(e: React.ChangeEvent<HTMLInputElement>) {
    const { searchParameters, onSearchParametersChanged } = this.props;
    if (e.target.value) {
      searchParameters.searchLimit = parseInt(e.target.value, 10) || undefined;
    } else {
      searchParameters.searchLimit = undefined;
    }

    onSearchParametersChanged(searchParameters);
  }

  @autobind
  async handleSortFieldChanged(newSortField: VideoSortField) {
    const { searchParameters, onSearchParametersChanged } = this.props;
    searchParameters.sortField = newSortField;

    onSearchParametersChanged(searchParameters);
  }

  @autobind
  async handleSortOrderChanged(newSortOrder: SortOrder) {
    const { searchParameters, onSearchParametersChanged } = this.props;
    searchParameters.sortOrder = newSortOrder;

    onSearchParametersChanged(searchParameters);
  }

  render() {
    const { accountId, connectorContext, searchParameters, onSearchForVideos, showSearchTarget, labelClass, controlClass, formControlClass } = this.props;
    const { searchTerms, searchTarget, sortField, sortOrder } = searchParameters;

    const shouldShowSearchTarget = showSearchTarget === undefined || showSearchTarget === true;

    return (
      <>
        {shouldShowSearchTarget && (
          <div className="row">
            <div className="form-group col-md-5">
              <label>Search</label>
              <div>
                <div className="input-group">
                  <input
                    type="search"
                    placeholder="Search"
                    className={`form-control ${formControlClass}`}
                    value={searchTerms}
                    onKeyPress={e => e.key === 'Enter' && onSearchForVideos(accountId, searchParameters)}
                    onChange={this.handleSearchTermsChange}
                  />
                  <div className="input-group-btn">
                    <Button onClick={() => onSearchForVideos(accountId, searchParameters)}>
                      &nbsp;
                      <i className="glyphicon glyphicon-search" />
                    </Button>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-md-1" />
            <div className={`form-group col-md-6`}>
              <label>Target</label>
              <div>
                <div className="input-group">
                  <label className="radio-inline">
                    <input type="radio" checked={searchTarget === SearchTarget.SharePoint} onChange={() => this.handleSearchTargetChanged(SearchTarget.SharePoint)} />
                    SharePoint only
                  </label>
                  <label className="radio-inline">
                    <input type="radio" checked={searchTarget === SearchTarget.VideoCloud} onChange={() => this.handleSearchTargetChanged(SearchTarget.VideoCloud)} />
                    Video Cloud only
                  </label>
                </div>
              </div>
            </div>
          </div>
        )}
        {!shouldShowSearchTarget && (
          <div className="form-group">
            <label className={labelClass}>Available Videos:</label>
            <div className={controlClass}>
              <div className="input-group">
                <input
                  type="search"
                  placeholder="Video Filter"
                  className={`form-control ${formControlClass}`}
                  value={searchTerms}
                  onKeyPress={e => e.key === 'Enter' && onSearchForVideos(accountId, searchParameters)}
                  onChange={this.handleSearchTermsChange}
                />
                <div className="input-group-btn">
                  <Button bsSize={formControlClass && formControlClass.indexOf('input-sm') > -1 ? 'small' : undefined} onClick={() => onSearchForVideos(accountId, searchParameters)}>
                    &nbsp;
                    <i className="glyphicon glyphicon-search" />
                  </Button>
                </div>
              </div>
            </div>
          </div>
        )}
        <div className="form-group">
          <label className={labelClass}>Folder: </label>
          <div className={controlClass}>
            <FolderPicker
              accountId={accountId}
              className={formControlClass}
              connectorContext={connectorContext}
              selectedFolderId={searchParameters.folderId}
              onChange={this.handleSelectedFolderChange}
            />
          </div>
        </div>
        <div className="form-group">
          <label className={labelClass}>Limit: </label>
          <div className={controlClass}>
            <input type="number" value={searchParameters.searchLimit} onChange={this.handleSearchLimitChange} />
          </div>
        </div>
        <div className="form-group">
          <label className={labelClass}>Sort results by: </label>
          <div className={controlClass}>
            <div className="input-group">
              <label className="radio-inline">
                <input type="radio" checked={sortField === VideoSortField.Name} onChange={() => this.handleSortFieldChanged(VideoSortField.Name)} />
                display name
              </label>
              <label className="radio-inline">
                <input type="radio" checked={sortField === VideoSortField.UpdatedDate} onChange={() => this.handleSortFieldChanged(VideoSortField.UpdatedDate)} />
                updated date
              </label>
              <label className="radio-inline">
                <input type="radio" checked={sortField === VideoSortField.CreationDate} onChange={() => this.handleSortFieldChanged(VideoSortField.CreationDate)} />
                creation date
              </label>
              <br />
              <label className="radio-inline">
                <input type="radio" checked={sortField === VideoSortField.StartDate} onChange={() => this.handleSortFieldChanged(VideoSortField.StartDate)} />
                start date
              </label>
              <label className="radio-inline">
                <input type="radio" checked={sortField === VideoSortField.TotalPlays} onChange={() => this.handleSortFieldChanged(VideoSortField.TotalPlays)} />
                total plays
              </label>
            </div>
          </div>
        </div>
        <div className="form-group">
          <label className={labelClass}>Order: </label>
          <div className={controlClass}>
            <div className="input-group">
              <label className="radio-inline">
                <input type="radio" checked={sortOrder === SortOrder.Ascending} onChange={() => this.handleSortOrderChanged(SortOrder.Ascending)} />
                ascending
              </label>
              <label className="radio-inline">
                <input type="radio" checked={sortOrder === SortOrder.Descending} onChange={() => this.handleSortOrderChanged(SortOrder.Descending)} />
                descending
              </label>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export interface VideoSearchControlsProps {
  accountId: string;
  connectorContext: ConnectorContext;
  showSearchTarget?: boolean;
  labelClass?: string;
  controlClass?: string;
  formControlClass?: string;
  searchParameters: Partial<VideoSearchParameters>;
  onSearchParametersChanged: (searchParameters: Partial<VideoSearchParameters>, fieldUpdated?: string) => void;
  onSearchForVideos: (accountId: string, searchParameters: Partial<VideoSearchParameters>) => void;
}
