import * as React from 'react';
import { HashRouter as Router, Switch, Route } from 'react-router-dom';
import NotificationSystem from 'react-notification-system';
import autobind from 'autobind-decorator';

import { Mini } from './Mini';
import { Part } from './Part';
import { Manage } from './Manage';

import { ConnectorContext } from './services/connector';

export class App extends React.Component<{}, AppState> {
  private _notificationSystem: NotificationSystem.System | undefined;

  constructor(props: {}, context?: any) {
    super(props, context);

    const connectorContext = new ConnectorContext(this.raiseError);

    this.state = {
      connectorContext
    };

    // Redirect to mini routes based on query string.
    const { spAddinContext } = connectorContext;
    if (spAddinContext.mini) {
      switch (spAddinContext.mini) {
        case 'videos':
          window.location.hash = '#/mini/videos';
          break;
        case 'playlists':
          window.location.hash = '#/mini/playlists';
          break;
        case 'experiences':
          window.location.hash = '#/mini/experiences';
          break;
        default:
          // Do Nothing.
          break;
      }
    }

    // Redirect to part routes based on query string.
    if (spAddinContext.part) {
      switch (spAddinContext.part) {
        case 'experience':
          window.location.hash = '#part/experience';
          break;
        case 'playlist':
          window.location.hash = '#/part/playlist';
          break;
        case 'video':
          window.location.hash = '#/part/video';
          break;
        default:
          // Do Nothing.
          break;
      }
    }
  }

  render() {
    const { connectorContext } = this.state;

    return (
      <Router>
        <>
          <Switch>
            <Route path="/mini/:area" exact={true}>
              <Mini connectorContext={connectorContext} raiseError={this.raiseError} />
            </Route>
            <Route path="/part/:kind" exact={true}>
              <Part connectorContext={connectorContext} raiseError={this.raiseError} />
            </Route>
            <Route>
              <Manage connectorContext={connectorContext} raiseError={this.raiseError} />
            </Route>
          </Switch>
          <NotificationSystem ref={(ns: NotificationSystem.System) => this.setNotificationSystem(ns)} />
        </>
      </Router>
    );
  }

  @autobind
  setNotificationSystem(ns: NotificationSystem.System) {
    this._notificationSystem = ns;
  }

  @autobind
  private raiseError(message: string): boolean {
    if (!this._notificationSystem) {
      return true;
    }

    this._notificationSystem.addNotification({
      title: 'An error has occurred',
      message: message,
      level: 'error',
      position: 'br',
      autoDismiss: 0
    });
    return false;
  }
}

interface AppState {
  connectorContext: ConnectorContext;
}
