import * as React from 'react';
import { useState, useEffect } from 'react';
import { FormControl } from 'react-bootstrap';

import { BCPlayer } from '@brightcove/video-cloud-sdk';

import { get, find, filter, orderBy } from 'lodash';

export const PlayerPicker = (props: PlayerPickerProps) => {
  const { accountId, retrieveAllPlayers, playerFilter, playerType, defaultPlayerId, selectedPlayerId, onChange, placeholder, className } = props;
  const [isLoadingPlayers, setIsLoadingPlayers] = useState(true);
  const [allPlayers, setAllPlayers] = useState<BCPlayer[]>([]);
  const [filteredPlayers, setFilteredPlayers] = useState<BCPlayer[]>([]);
  const [, setSelectedPlayer] = useState<BCPlayer>();

  const filterPlayers = (allPlayers: Array<BCPlayer>): { filteredPlayers: Array<BCPlayer>; selectedPlayer?: BCPlayer } => {
    // If a player filter string is specified, filter the players by it.
    let filteredPlayers = allPlayers;
    if (playerFilter && playerFilter.length > 0) {
      const strFilter = playerFilter.toLowerCase();
      filteredPlayers = filter(filteredPlayers, player => {
        if (player.name && player.name.toLowerCase().indexOf(strFilter) > -1) {
          return true;
        }
        return false;
      });
    }

    // Filter players by selected type.
    filteredPlayers = filter(filteredPlayers, player => {
      let isPlaylistPlayer = false;

      const playlistSetting = get(player, 'branches.master.configuration.playlist');
      if (playlistSetting) {
        isPlaylistPlayer = true;
      }

      if (!isPlaylistPlayer) {
        const plugins = get(player, 'branches.master.configuration.plugins');
        if (plugins) {
          // eslint-disable-next-line @typescript-eslint/camelcase
          const bcPlaylistUIPlayers = filter(plugins, { registry_id: '@brightcove/videojs-bc-playlist-ui' });
          if (bcPlaylistUIPlayers.length > 0) {
            isPlaylistPlayer = true;
          }
        }
      }

      if (playerType === 'Video' && isPlaylistPlayer) {
        return false;
      }

      if (playerType === 'Playlist' && !isPlaylistPlayer) {
        return false;
      }

      return true;
    });

    // Sort the players alphabetically by name with the default player on top.
    filteredPlayers = orderBy(filteredPlayers, ['name']);
    const defaultPlayerIndex = filteredPlayers.findIndex(p => p.id === 'default');
    if (defaultPlayerIndex > 0) {
      const defaultPlayer = filteredPlayers.splice(defaultPlayerIndex, 1);
      filteredPlayers.splice(0, 0, defaultPlayer[0]);
    }

    // Select the player based on the set we have now.
    let selectedPlayer: BCPlayer | undefined = undefined;
    if (filteredPlayers && filteredPlayers.length > 0) {
      selectedPlayer = find(filteredPlayers, { id: defaultPlayerId || selectedPlayerId || 'default' });
      if (!selectedPlayer) {
        selectedPlayer = filteredPlayers[0];
      }
      if (selectedPlayer && selectedPlayer.id !== selectedPlayerId && onChange) {
        onChange(selectedPlayer);
      }
    }

    return {
      filteredPlayers,
      selectedPlayer
    };
  };

  const loadPlayers = async () => {
    if (!accountId) {
      return;
    }

    setIsLoadingPlayers(true);

    const allPlayers = await retrieveAllPlayers(accountId);
    const filterPlayerResult = filterPlayers(allPlayers);

    setAllPlayers(allPlayers);
    setFilteredPlayers(filterPlayerResult.filteredPlayers);
    setSelectedPlayer(filterPlayerResult.selectedPlayer);

    setIsLoadingPlayers(false);
  };

  const handlePlayerChange = (event: React.FormEvent<FormControl>) => {
    const target = event.target as any;

    const selectedPlayer = find(allPlayers, { id: target.value });
    setSelectedPlayer(selectedPlayer);
    if (selectedPlayer && onChange) {
      onChange(selectedPlayer);
    }
  };

  useEffect(() => {
    if (isLoadingPlayers || !allPlayers) {
      loadPlayers();
    }
  }, [accountId, isLoadingPlayers]);

  useEffect(() => {
    const filterPlayerResult = filterPlayers(allPlayers);
    setFilteredPlayers(filterPlayerResult.filteredPlayers);
  }, [playerFilter]);

  if (!accountId) {
    return (
      <FormControl className={className} componentClass="select" disabled={true} style={{ width: '100%' }}>
        <option>Please select an account...</option>
      </FormControl>
    );
  }

  if (isLoadingPlayers) {
    return (
      <FormControl className={className} componentClass="select" disabled={true} style={{ width: '100%' }}>
        <option>Loading Players...</option>
      </FormControl>
    );
  }

  if ((!filteredPlayers || filteredPlayers.length <= 0) && playerFilter && playerFilter.length > 0) {
    return (
      <FormControl className={className} componentClass="select" disabled={true} style={{ width: '100%' }}>
        <option>All players were filtered out</option>
      </FormControl>
    );
  }

  if (!filteredPlayers || filteredPlayers.length <= 0) {
    return (
      <FormControl className={className} componentClass="select" disabled={true} style={{ width: '100%' }}>
        <option>There are no players available for this content.</option>
      </FormControl>
    );
  }

  let bcPlaceholder = placeholder;
  if (!bcPlaceholder) {
    bcPlaceholder = 'Select a player...';
  }

  let bcPlayerId = selectedPlayerId;
  if (!bcPlayerId) {
    bcPlayerId = 'default';
  }

  return (
    <FormControl className={className} componentClass="select" placeholder={bcPlaceholder} value={bcPlayerId} onChange={handlePlayerChange} style={{ width: '100%' }}>
      {filteredPlayers.map((player, i) => (
        <option key={i} value={player.id}>
          {player.name}
        </option>
      ))}
    </FormControl>
  );
};

export interface PlayerPickerProps {
  retrieveAllPlayers: (accountId: string) => Promise<BCPlayer[]>;
  accountId?: string;
  className?: string;
  placeholder?: string;
  playerFilter?: string;
  playerType: 'Video' | 'Playlist';
  defaultPlayerId?: string;
  selectedPlayerId?: string;
  onChange?: (selectedPlayer: BCPlayer) => void;
}
