import * as React from 'react';
import { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { Button, Modal } from 'react-bootstrap';
import { BootstrapTable, TableHeaderColumn } from 'react-bootstrap-table';
import { AccountPicker } from '../common';
import { PlaylistEmbed, EmbedLayoutMode } from '../embeds';
import { Spinner } from '../common';
import { PlaylistSearchControls } from '.';

import { BrightcoveAccount } from 'services';
import { BrightcoveApiGateway, BCPlaylist, BCPlaylistType } from '@brightcove/video-cloud-sdk';
import { SharePointApiGateway } from '../../services/sharepoint';
import { ConnectorContext, PlaylistSearchParameters, getDefaultPlaylistSearchParameters } from '../../services/connector';
import { BrightcovePlayerUtils, BrightcovePlayerProps } from '../brightcove-player';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlusCircle } from '@fortawesome/free-solid-svg-icons';

import { find } from 'lodash';

import './ManagePlaylists.css';

export const ManagePlaylists = (props: ManagePlaylistProps) => {
  const [isAddingPlaylist] = useState(false);
  const [isLoadingPlaylists, setIsLoadingPlaylists] = useState(true);
  const [isEmbedModalShowing, setIsEmbedModalShowing] = useState(false);
  const [searchParameters, setSearchParameters] = useState<Partial<PlaylistSearchParameters>>(getDefaultPlaylistSearchParameters());
  const [defaultPlaylistPlayerId, setDefaultPlaylistPlayerId] = useState('');
  const [playlists, setPlaylists] = useState<BCPlaylist[]>([]);
  const [embedPlaylistTitle, setEmbedPlaylistTitle] = useState<string>();
  const [embedPlayerProps, setEmbedPlayerProps] = useState<Partial<BrightcovePlayerProps>>(BrightcovePlayerUtils.getDefaultBrightcovePlaylistProps());

  const { accountId, connectorContext, onAccountChange } = props;
  const { bcApiGateway } = connectorContext;

  const searchForPlaylists = async (accountId: string, searchParams: Partial<PlaylistSearchParameters>): Promise<void> => {
    if (!accountId) {
      return;
    }

    setIsLoadingPlaylists(true);

    const playlistsApi = bcApiGateway.playlistsApi;

    const bcParams = connectorContext.getBCPlaylistSearchParams(searchParams);
    const playlists = await playlistsApi.getPlaylists(accountId, bcParams);

    setPlaylists(playlists);
    setIsLoadingPlaylists(false);
  };

  const handleAccountChange = (newAccount: BrightcoveAccount) => {
    if (accountId !== newAccount.accountId && onAccountChange) {
      onAccountChange(newAccount.accountId);
    }
  };

  const handleSearchParametersChanged = async (searchParameters: Partial<PlaylistSearchParameters>) => {
    setSearchParameters(searchParameters);

    await searchForPlaylists(accountId, searchParameters);
  };

  const handleEmbedPlayerPropsChanged = (embedPlayerProps: Partial<BrightcovePlayerProps>) => {
    setEmbedPlayerProps(embedPlayerProps);
  };

  const handleShowEmbedModal = (playlistId: string, playlistTitle: string) => {
    //const embedPlayerProps = BrightcovePlayerUtils.getDefaultBrightcovePlaylistProps();
    embedPlayerProps.accountId = accountId;
    embedPlayerProps.playerId = defaultPlaylistPlayerId;
    embedPlayerProps.modeId = playlistId;
    embedPlayerProps.sharepointVersion = connectorContext.spAddinContext.SPProductNumber;

    setEmbedPlaylistTitle(playlistTitle);
    setEmbedPlayerProps(embedPlayerProps);
    setIsEmbedModalShowing(true);
  };

  const hideEmbedModal = () => {
    setEmbedPlaylistTitle(undefined);
    setEmbedPlayerProps(BrightcovePlayerUtils.getDefaultBrightcovePlaylistProps());
    setIsEmbedModalShowing(false);
  };

  const renderPlaylistNameCell = (cell: any, playlist: BCPlaylist): React.ReactElement<any> => {
    const editPlaylistUrl = `/${accountId}/manage-playlists/${playlist.id}/edit`;
    return (
      <>
        <Link to={editPlaylistUrl}>{playlist.name}</Link>
        <br />
        {playlist.video_ids && playlist.video_ids.length > 0 && (
          <>
            <div style={{ fontSize: 'small' }}>Video IDs in list: </div>
            <div style={{ fontSize: 'xx-small' }}>
              {playlist.video_ids.map((videoId, ix) => (
                <span key={ix} style={{ paddingLeft: '5px', float: 'left' }}>
                  {videoId}
                </span>
              ))}
            </div>
          </>
        )}
        {(!playlist.video_ids || playlist.video_ids.length === 0) && playlist.type === BCPlaylistType.Manual && <i>This playlist does not contain any videos.</i>}
        {(!playlist.video_ids || playlist.video_ids.length === 0) && playlist.type !== BCPlaylistType.Manual && <i>This playlist is a Smart Playlist</i>}
      </>
    );
  };

  const renderEditLink = (cell: any, playlist: BCPlaylist): React.ReactElement<any> => {
    const editPlaylistUrl = `/${accountId}/manage-playlists/${playlist.id}/edit`;

    return (
      <div className="result text-center">
        <div>
          <Link className="btn btn-link" style={{ width: '70px' }} to={editPlaylistUrl}>
            Edit
          </Link>
        </div>
        <div>
          <Button bsStyle="success" style={{ width: '70px' }} bsSize="small" onClick={() => handleShowEmbedModal(playlist.id, playlist.name)}>
            Embed
          </Button>
        </div>
      </div>
    );
  };

  useEffect(() => {
    const selectedAccount = find(connectorContext.availableAccounts, account => {
      return account.accountId === accountId;
    });

    if (selectedAccount) {
      setDefaultPlaylistPlayerId(selectedAccount.defaultPlaylistPlayerId);
    }

    searchForPlaylists(accountId, searchParameters);
  }, [accountId, searchParameters]);

  const labelClass = 'col-md-3 control-label';
  const controlClass = 'col-md-9';

  if (isAddingPlaylist) {
    return <Spinner loadingText="Creating New Playlist..." />;
  }

  return (
    <>
      <ol className="breadcrumb">
        <li>
          <Link to="/">Brightcove Management</Link>
        </li>
        <li className="active">Manage Playlists</li>
      </ol>
      <div className="search form-horizontal">
        <div className="form-group">
          <label className={labelClass}>Select Account:</label>{' '}
          <div className={controlClass}>
            <AccountPicker availableAccounts={connectorContext.availableAccounts} selectedAccountId={accountId} onChange={handleAccountChange} />
          </div>
        </div>
      </div>
      <div className="search form-horizontal">
        {accountId && (
          <>
            <PlaylistSearchControls
              accountId={accountId}
              labelClass={labelClass}
              controlClass={controlClass}
              searchParameters={searchParameters}
              onSearchParametersChanged={handleSearchParametersChanged}
              onSearchForPlaylists={searchForPlaylists}
            />
            {isLoadingPlaylists && <Spinner loadingText="Loading Playlists..." />}
            {!isLoadingPlaylists && playlists && (
              <>
                <BootstrapTable data={playlists} striped={true} bordered={false}>
                  <TableHeaderColumn dataField="name" dataFormat={renderPlaylistNameCell} dataSort={true}>
                    Name
                  </TableHeaderColumn>
                  <TableHeaderColumn dataField="description" dataSort={true}>
                    Short Description
                  </TableHeaderColumn>
                  <TableHeaderColumn
                    dataField="type"
                    dataSort={true}
                    dataFormat={cell => {
                      return cell === BCPlaylistType.Manual ? 'Manual Playlist' : 'Smart Playlist';
                    }}
                  >
                    Type
                  </TableHeaderColumn>
                  <TableHeaderColumn dataField="id" dataSort={true} isKey={true}>
                    ID
                  </TableHeaderColumn>
                  <TableHeaderColumn width="90" dataFormat={renderEditLink}>
                    Edit Link
                  </TableHeaderColumn>
                </BootstrapTable>
                <hr />
                <Link className="btn btn-default btn-lg" to={`/${accountId}/manage-playlists/new`}>
                  <FontAwesomeIcon icon={faPlusCircle} />
                  &nbsp;Add New Playlist
                </Link>
                <hr />
              </>
            )}
            <Modal className="embed" show={isEmbedModalShowing} onHide={hideEmbedModal} bsSize="large">
              <Modal.Header closeButton={true}>
                <Modal.Title>Embed Playlist</Modal.Title>
              </Modal.Header>
              <Modal.Body>
                {embedPlayerProps && (
                  <PlaylistEmbed
                    layoutMode={EmbedLayoutMode.Full}
                    bcPlayerProps={embedPlayerProps}
                    playlistTitle={embedPlaylistTitle || ''}
                    defaultPlaylistPlayerId={defaultPlaylistPlayerId}
                    doesEmbedHelperExist={connectorContext.doesEmbedHelperExist}
                    spAddinContext={connectorContext.spAddinContext}
                    spApiGateway={connectorContext.spApiGateway}
                    showUploadWebPartButton={false}
                    retrieveAllPlayers={connectorContext.retrieveAllPlayers}
                    onPlayerPropsChanged={handleEmbedPlayerPropsChanged}
                  />
                )}
              </Modal.Body>
              <Modal.Footer />
            </Modal>
          </>
        )}
      </div>
    </>
  );
};

interface ManagePlaylistProps {
  accountId: string;
  connectorContext: ConnectorContext;
  bcApiGateway: BrightcoveApiGateway;
  spApiGateway: SharePointApiGateway;
  onAccountChange?: (accountId: string) => void;
}
