import * as React from 'react';
import { Button } from 'react-bootstrap';
import { BootstrapTable, TableHeaderColumn, SelectRow, Options } from 'react-bootstrap-table';
import autobind from 'autobind-decorator';
import { AccountPicker, Spinner } from '../common';

import { BrightcoveAccount } from 'services';
import { BCExperience } from '@brightcove/video-cloud-sdk';

import URI from 'urijs';
import { filter, find } from 'lodash';
import { ConnectorContext } from '../../services/connector';
import { BrightcoveExperienceProps } from '../brightcove-experience';
import { ExperienceEmbed, EmbedLayoutMode } from '../embeds';

export class EditPartExperience extends React.Component<EditPartExperienceProps, EditPartExperienceState> {
  constructor(props: EditPartExperienceProps, context?: any) {
    super(props, context);

    const bcExperienceProps = props.experienceProps;
    bcExperienceProps.sharepointVersion = this.props.connectorContext.spAddinContext.SPProductNumber;

    const uri = URI();
    const info = uri.query(true) as { [key: string]: string };

    this.state = {
      bcExperienceProps,
      experiencesFilter: '',
      isLoadingExperiences: true,
      isSaving: false,
      allExperiences: [],
      filteredExperiences: [],
      webPartId: info.wpId
    };
  }

  async UNSAFE_componentWillMount() {
    const { connectorContext } = this.props;
    const { bcExperienceProps } = this.state;
    if (!bcExperienceProps.accountId && connectorContext.availableAccounts && connectorContext.availableAccounts.length > 0) {
      bcExperienceProps.accountId = connectorContext.availableAccounts[0].accountId;
    }

    this.loadExperiences(bcExperienceProps.accountId || '');

    this.setState({
      bcExperienceProps
    });
  }

  async loadExperiences(accountId: string) {
    const { connectorContext } = this.props;
    const { bcExperienceProps } = this.state;

    if (!accountId) {
      return;
    }

    this.setState({
      isLoadingExperiences: true,
      experiencesFilter: ''
    });

    const experiencesApi = connectorContext.bcApiGateway.experiencesApi;
    const experiencesResponse = await experiencesApi.getExperiences(accountId);

    let selectedExperience: BCExperience | undefined = undefined;
    if (bcExperienceProps.experienceId) {
      selectedExperience = find(experiencesResponse.items, { id: bcExperienceProps.experienceId });
    }

    this.setState({
      isLoadingExperiences: false,
      allExperiences: experiencesResponse.items,
      filteredExperiences: experiencesResponse.items,
      selectedExperience
    });
  }

  @autobind
  async handleAccountChange(newAccount: BrightcoveAccount) {
    const { bcExperienceProps } = this.state;

    if (bcExperienceProps.accountId !== newAccount.accountId) {
      bcExperienceProps.accountId = newAccount.accountId;
      this.setState({
        bcExperienceProps
      });
      this.loadExperiences(newAccount.accountId);
    }
  }

  @autobind
  handleExperiencesFilterChange(experiencesFilter: string) {
    this.setState({
      experiencesFilter
    });

    const { allExperiences } = this.state;
    let filteredExperiences = allExperiences;
    if (experiencesFilter) {
      const strFilter = experiencesFilter.toLowerCase();
      filteredExperiences = filter(filteredExperiences, ex => {
        if (ex.name && ex.name.toLowerCase().indexOf(strFilter) > -1) {
          return true;
        }
        return false;
      });
    }

    this.setState({
      filteredExperiences
    });
  }

  @autobind
  experienceSelected(experience: BCExperience, isSelected: boolean) {
    const { bcExperienceProps } = this.state;
    bcExperienceProps.embedCodeOverride = undefined;

    let selectedExperience: BCExperience | undefined;
    if (isSelected) {
      selectedExperience = experience;
      bcExperienceProps.experienceId = selectedExperience.id;
    }
    this.setState({
      bcExperienceProps,
      selectedExperience
    });
  }

  @autobind
  handleExperiencePropsChanged(bcExperienceProps: Partial<BrightcoveExperienceProps>) {
    this.setState({
      bcExperienceProps
    });
  }

  @autobind
  renderExperienceNameCell(cell: any, experience: BCExperience) {
    const { bcExperienceProps } = this.state;

    let uri = URI(`${window.location.origin}${window.location.pathname.replace(/AppPartPage.aspx/i, 'Default.aspx')}${window.location.search}`);
    uri = uri.removeQuery('part');
    uri = uri.removeQuery('IsDlg');
    uri.hash(`/${bcExperienceProps.accountId}/manage-experiences/${experience.id}/preview`);

    return (
      <>
        <a className="small" href={uri.toString()} target="_blank" rel="noopener noreferrer">
          {experience.name}
        </a>
        <br />
        <div className="small">{experience.template}</div>
        <div className="small">{experience.id}</div>
      </>
    );
  }

  @autobind
  async applyWebpartChanges() {
    this.setState({
      isSaving: true
    });

    const { connectorContext } = this.props;
    const { spApiGateway } = connectorContext;
    const { bcExperienceProps, selectedExperience, webPartId } = this.state;

    if (!selectedExperience) {
      return;
    }

    bcExperienceProps.experienceId = selectedExperience.id || '';

    await spApiGateway.spWebPartConfig.setBrightcoveWebPartProps(webPartId, bcExperienceProps);
    window.location.reload(true);
  }

  @autobind
  async cancelWebpartChanges() {
    window.location.reload(true);
  }

  render() {
    const { connectorContext } = this.props;

    const { bcExperienceProps, experiencesFilter, isLoadingExperiences, isSaving, filteredExperiences, selectedExperience } = this.state;

    const labelClass = 'col-xs-3 control-label small';
    const controlClass = 'col-xs-9 word-wrap small';

    const selectRow: SelectRow = {
      mode: 'radio',
      clickToSelect: true,
      className: 'info',
      onSelect: this.experienceSelected
    };

    if (bcExperienceProps.experienceId) {
      selectRow.selected = [bcExperienceProps.experienceId];
    }

    if (selectedExperience) {
      selectRow.selected = [selectedExperience.id];
    }

    const experienceTableOptions: Options = {
      defaultSortName: 'updatedAt', // default sort column name
      defaultSortOrder: 'desc' // default sort order
    };

    const canApply = !isLoadingExperiences && bcExperienceProps.accountId && selectedExperience;

    return (
      <div className="form-horizontal">
        <div className="form-group">
          <label className={labelClass}>Brightcove Account:</label>
          <div className={controlClass}>
            <AccountPicker className="input-sm" availableAccounts={connectorContext.availableAccounts} selectedAccountId={bcExperienceProps.accountId} onChange={this.handleAccountChange} />
          </div>
        </div>
        <div className="form-group">
          <label className={labelClass}>Experiences Filter:</label>
          <div className={controlClass}>
            <div className="input-group">
              <input type="search" placeholder="Search" className="form-control input-sm" value={experiencesFilter} onChange={e => this.handleExperiencesFilterChange(e.target.value)} />
              <div className="input-group-btn searchButton">
                <Button bsSize="small">
                  &nbsp;
                  <i className="glyphicon glyphicon-search" />
                </Button>
              </div>
            </div>
          </div>
        </div>
        {isLoadingExperiences && bcExperienceProps.accountId && <Spinner loadingText="Loading Experiences..." />}
        {!isLoadingExperiences && bcExperienceProps.accountId && (
          <>
            <div className="well">
              <BootstrapTable data={filteredExperiences} striped={true} bordered={false} selectRow={selectRow} options={experienceTableOptions} height="250">
                <TableHeaderColumn dataField="name" dataFormat={this.renderExperienceNameCell} dataSort={true}>
                  Name
                </TableHeaderColumn>
                <TableHeaderColumn dataField="description" dataSort={true} hidden={true}>
                  Short Description
                </TableHeaderColumn>
                <TableHeaderColumn dataField="updatedAt" dataSort={true} hidden={true}>
                  Updated At
                </TableHeaderColumn>
                <TableHeaderColumn dataField="id" dataSort={true} isKey={true} hidden={true}>
                  ID
                </TableHeaderColumn>
              </BootstrapTable>
              <hr />
            </div>
            <ExperienceEmbed
              bcExperienceProps={bcExperienceProps}
              layoutMode={EmbedLayoutMode.Part}
              connectorContext={connectorContext}
              experience={selectedExperience}
              hideExperienceName={true}
              labelClass={labelClass}
              controlClass={controlClass}
              showUploadWebPartButton={false}
              onExperiencePropsChanged={this.handleExperiencePropsChanged}
            />
          </>
        )}
        <div className="col-xs-12" style={{ paddingTop: '5px' }}>
          {isSaving && <Spinner loadingText="Saving..." />}
          {!isSaving && (
            <>
              <Button className="pull-right" bsStyle="primary" disabled={!canApply} style={{ margin: '2px' }} onClick={this.applyWebpartChanges}>
                Apply
              </Button>
              <Button className="pull-right" style={{ margin: '2px' }} onClick={this.cancelWebpartChanges}>
                Cancel
              </Button>
            </>
          )}
        </div>
      </div>
    );
  }
}

interface EditPartExperienceProps {
  connectorContext: ConnectorContext;
  experienceProps: Partial<BrightcoveExperienceProps>;
}

interface EditPartExperienceState {
  bcExperienceProps: Partial<BrightcoveExperienceProps>;
  selectedExperience?: BCExperience;
  experiencesFilter: string;
  isLoadingExperiences: boolean;
  isSaving: boolean;
  allExperiences: Array<BCExperience>;
  filteredExperiences: Array<BCExperience>;
  webPartId: string;
}
