import { SortOrder } from '.';

export enum SearchTarget {
  SharePoint = 'SharePoint',
  VideoCloud = 'VideoCloud'
}

export enum VideoSortField {
  Name = 'name',
  CreationDate = 'creationDate',
  StartDate = 'startDate',
  UpdatedDate = 'updatedDate',
  TotalPlays = 'totalPlays'
}

export interface VideoSearchParameters {
  searchTerms: string;
  searchTarget: SearchTarget;
  searchLimit: number;
  folderId: string;
  sortField: VideoSortField;
  sortOrder: SortOrder;
}

export function getDefaultVideoSearchParameters() {
  return {
    searchTerms: '',
    searchTarget: SearchTarget.VideoCloud,
    searchLimit: 30,
    sortField: VideoSortField.UpdatedDate,
    sortOrder: SortOrder.Descending
  };
}
