import * as React from 'react';
import { useState, useEffect } from 'react';
import useDebounce from '../../util/useDebounce';
import { Button } from 'react-bootstrap';

import { PlaylistSortField, SortOrder, PlaylistSearchParameters } from '../../services/connector';

export const PlaylistSearchControls = (props: PlaylistSearchControlsProps) => {
  const { accountId, searchParameters, onSearchForPlaylists, onSearchParametersChanged, labelClass, controlClass, formControlClass } = props;
  const [searchTerm, setSearchTerm] = useState(searchParameters.searchTerms);
  const { sortField, sortOrder } = searchParameters;

  const debouncedSearchTerm = useDebounce(searchTerm, 500);

  useEffect(() => {
    searchParameters.searchTerms = debouncedSearchTerm;
    onSearchParametersChanged(searchParameters, 'searchTerms');
  }, [debouncedSearchTerm]);

  const handleSortFieldChanged = (newSortField: PlaylistSortField) => {
    searchParameters.sortField = newSortField;

    onSearchParametersChanged(searchParameters);
  };

  const handleSortOrderChanged = (newSortOrder: SortOrder) => {
    searchParameters.sortOrder = newSortOrder;

    onSearchParametersChanged(searchParameters);
  };

  return (
    <>
      <div className="form-group">
        <label className={labelClass}>Available Playlists:</label>
        <div className={controlClass}>
          <div className="input-group">
            <input
              type="search"
              placeholder="Playlist Filter"
              className={`form-control ${formControlClass}`}
              value={searchTerm}
              onKeyPress={e => e.key === 'Enter' && setSearchTerm(searchTerm)}
              onChange={e => setSearchTerm(e.target.value)}
            />
            <div className="input-group-btn">
              <Button bsSize={formControlClass === 'input-xs' ? 'small' : undefined} onClick={() => onSearchForPlaylists(accountId, searchParameters)}>
                &nbsp;
                <i className="glyphicon glyphicon-search" />
              </Button>
            </div>
          </div>
        </div>
      </div>
      <div className="form-group">
        <label className={labelClass}>Sort results by: </label>
        <div className={controlClass}>
          <div className="input-group">
            <label className="radio-inline">
              <input type="radio" checked={sortField === PlaylistSortField.DisplayName} onChange={() => handleSortFieldChanged(PlaylistSortField.DisplayName)} />
              display name
            </label>
            <label className="radio-inline">
              <input type="radio" checked={sortField === PlaylistSortField.ModifiedDate} onChange={() => handleSortFieldChanged(PlaylistSortField.ModifiedDate)} />
              modified date
            </label>
          </div>
        </div>
      </div>
      <div className="form-group">
        <label className={labelClass}>Order: </label>
        <div className={controlClass}>
          <div className="input-group">
            <label className="radio-inline">
              <input type="radio" checked={sortOrder === SortOrder.Ascending} onChange={() => handleSortOrderChanged(SortOrder.Ascending)} />
              ascending
            </label>
            <label className="radio-inline">
              <input type="radio" checked={sortOrder === SortOrder.Descending} onChange={() => handleSortOrderChanged(SortOrder.Descending)} />
              descending
            </label>
          </div>
        </div>
      </div>
    </>
  );
};

export interface PlaylistSearchControlsProps {
  accountId: string;
  labelClass?: string;
  controlClass?: string;
  formControlClass?: string;
  searchParameters: Partial<PlaylistSearchParameters>;
  onSearchParametersChanged: (searchParameters: Partial<PlaylistSearchParameters>, fieldUpdated?: string) => void;
  onSearchForPlaylists: (accountId: string, searchParameters: Partial<PlaylistSearchParameters>) => void;
}
