import * as React from 'react';
import { useState } from 'react';

import { BrightcovePlayerProps, BrightcovePlayerAspectRatio } from '.';
import { Button } from 'react-bootstrap';
import { PlayerPicker } from '../common';

import { BCPlayer } from '@brightcove/video-cloud-sdk';

export const BrightcovePlayerControls = (props: BrightcovePlayerControlsProps) => {
  const {
    playerType,
    retrieveAllPlayers,
    defaultPlayerId,
    labelClass,
    controlClass,
    formControlClass,
    showEmbedCustomizations,
    showAdvancedEmbedCustomizations,
    bcPlayerProps,
    showUnitsCustomizations,
    onPlayerPropsChanged
  } = props;

  let { whClass } = props;
  const [playerFilter, setPlayerFilter] = useState('');

  const calculatePlayerHeight = (aspectRatio: BrightcovePlayerAspectRatio, playerWidth?: string, playerHeight?: string): string | undefined => {
    let newPlayerHeight = playerHeight;

    if (!playerWidth) {
      return '';
    }

    switch (aspectRatio) {
      case BrightcovePlayerAspectRatio.Widescreen:
        newPlayerHeight = Math.round((9 / 16) * parseFloat(playerWidth)).toString();
        break;
      case BrightcovePlayerAspectRatio.Standard:
        newPlayerHeight = Math.round((3 / 4) * parseFloat(playerWidth)).toString();
        break;
      default:
        // Do Nothing;
        break;
    }
    return newPlayerHeight;
  };

  const handleAutoplayChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    onPlayerPropsChanged({
      ...bcPlayerProps,
      autoplay: event.target.checked
    });
  };

  const handleMutedChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    onPlayerPropsChanged({
      ...bcPlayerProps,
      muted: event.target.checked
    });
  };

  const handlePlayerFilterChange = (value: string) => {
    setPlayerFilter(value);
  };

  const handlePlayerChange = (selectedPlayer: BCPlayer) => {
    onPlayerPropsChanged({
      ...bcPlayerProps,
      playerId: selectedPlayer.id
    });
  };

  const handleEmbedTypeChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    bcPlayerProps.embedType = event.target.value as 'Standard' | 'Advanced';
    if (bcPlayerProps.embedType === 'Standard') {
      bcPlayerProps.sizing = 'Fixed';
    }

    if (showAdvancedEmbedCustomizations === false) {
      bcPlayerProps.sizing = 'Responsive';
    } else {
      bcPlayerProps.sizing = 'Fixed';
    }

    onPlayerPropsChanged({
      ...bcPlayerProps
    });
  };

  const handleNumThumbnailsChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    if (event.target.value) {
      bcPlayerProps.playerNumThumbnails = parseInt(event.target.value, 10);
    } else {
      bcPlayerProps.playerNumThumbnails = undefined;
    }

    onPlayerPropsChanged({
      ...bcPlayerProps
    });
  };

  const handleSizingChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    bcPlayerProps.sizing = event.target.value as 'Responsive' | 'Fixed';
    onPlayerPropsChanged({
      ...bcPlayerProps
    });
  };

  const handleAspectRatioChange = (event: React.ChangeEvent<HTMLSelectElement>) => {
    const { playerWidth, playerHeight } = bcPlayerProps;

    bcPlayerProps.aspectRatio = event.target.value as BrightcovePlayerAspectRatio;
    if (bcPlayerProps.aspectRatio !== BrightcovePlayerAspectRatio.Custom) {
      bcPlayerProps.playerHeight = calculatePlayerHeight(bcPlayerProps.aspectRatio, playerWidth, playerHeight);
    } else {
      bcPlayerProps.playerHeight = '';
    }

    onPlayerPropsChanged({
      ...bcPlayerProps
    });
  };

  const handleWidthChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    bcPlayerProps.playerWidth = event.target.value || '';

    if (bcPlayerProps.aspectRatio !== BrightcovePlayerAspectRatio.Custom) {
      bcPlayerProps.playerHeight = calculatePlayerHeight(bcPlayerProps.aspectRatio || BrightcovePlayerAspectRatio.Widescreen, bcPlayerProps.playerWidth, bcPlayerProps.playerHeight);
    } else {
      bcPlayerProps.playerHeight = undefined;
    }

    onPlayerPropsChanged({
      ...bcPlayerProps
    });
  };

  const handleHeightChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    bcPlayerProps.playerHeight = event.target.value || '';

    onPlayerPropsChanged({
      ...bcPlayerProps
    });
  };

  const handleUnitsChange = (event: React.ChangeEvent<HTMLSelectElement>) => {
    bcPlayerProps.units = event.target.value as 'px' | 'em';

    onPlayerPropsChanged({
      ...bcPlayerProps
    });
  };

  if (!whClass) {
    whClass = 'col-md-6';
  }

  const allowFixed = true;
  let allowResponsive = true;
  if (playerType === 'Video') {
    if (bcPlayerProps.embedType === 'Standard') {
      allowResponsive = false;
    } else {
      allowResponsive = true;
    }
  } else {
    if (bcPlayerProps.embedType === 'Advanced') {
      allowResponsive = false;
    }
  }

  return (
    <>
      <div className="form-group">
        <label className={labelClass}>Options</label>
        <div className={controlClass}>
          <div className={`input-group ${formControlClass}`}>
            <label className={`checkbox-inline ${formControlClass}`}>
              <input type="checkbox" checked={bcPlayerProps.autoplay} onChange={handleAutoplayChange} />
              Autoplay
            </label>
            <label className={`checkbox-inline ${formControlClass}`}>
              <input type="checkbox" checked={bcPlayerProps.muted} onChange={handleMutedChange} />
              Muted
            </label>
          </div>
        </div>
      </div>
      <div className="form-group">
        <label className={labelClass}>Player Filter:</label>
        <div className={controlClass}>
          <div className="input-group">
            <input type="search" placeholder="Search" className="form-control input-sm" value={playerFilter} onChange={e => handlePlayerFilterChange(e.target.value)} />
            <div className="input-group-btn searchButton">
              <Button bsSize="small">
                &nbsp;
                <i className="glyphicon glyphicon-search" />
              </Button>
            </div>
          </div>
        </div>
      </div>
      <div className="form-group">
        <label className={labelClass}>Player:</label>
        <div className={controlClass}>
          <PlayerPicker
            className="input-sm"
            accountId={bcPlayerProps.accountId}
            retrieveAllPlayers={retrieveAllPlayers}
            playerType={playerType}
            playerFilter={playerFilter}
            defaultPlayerId={defaultPlayerId}
            selectedPlayerId={bcPlayerProps.playerId}
            onChange={handlePlayerChange}
          />
        </div>
      </div>
      {showEmbedCustomizations === true && (
        <>
          <div className="form-group">
            <label className={labelClass}>Embed Type</label>
            <div className={controlClass}>
              <div className={`input-group ${formControlClass}`}>
                <label className="radio-inline">
                  <input type="radio" name="embedType" value="Standard" checked={bcPlayerProps.embedType === 'Standard'} onChange={handleEmbedTypeChange} /> iFrame
                </label>
                <label className="radio-inline">
                  <input type="radio" name="embedType" value="Advanced" checked={bcPlayerProps.embedType === 'Advanced'} onChange={handleEmbedTypeChange} /> JavaScript
                </label>
              </div>
            </div>
          </div>
          {showAdvancedEmbedCustomizations === true && (
            <>
              {playerType === 'Playlist' && (
                <div className="form-group">
                  <label className={labelClass}>Number of Thumbnails</label>
                  <div className={controlClass}>
                    <input
                      className={`form-control ${formControlClass}`}
                      type="number"
                      placeholder="auto"
                      disabled={bcPlayerProps.embedType !== 'Advanced'}
                      value={bcPlayerProps.playerNumThumbnails}
                      onChange={handleNumThumbnailsChange}
                    />
                  </div>
                </div>
              )}
              <div className="form-group">
                <label className={labelClass}>Sizing</label>
                <div className={controlClass}>
                  <div className={`input-group ${formControlClass}`}>
                    <label className="radio-inline">
                      <input type="radio" name="sizing" value="Fixed" disabled={!allowFixed} checked={bcPlayerProps.sizing === 'Fixed'} onChange={handleSizingChange} /> Fixed
                    </label>
                    <label className="radio-inline">
                      <input type="radio" name="sizing" value="Responsive" disabled={!allowResponsive} checked={bcPlayerProps.sizing === 'Responsive'} onChange={handleSizingChange} /> Responsive
                    </label>
                  </div>
                </div>
              </div>
              <div className="form-group">
                <label className={labelClass}>Aspect Ratio</label>
                <div className={controlClass}>
                  <select className={`form-control ${formControlClass}`} value={bcPlayerProps.aspectRatio} onChange={handleAspectRatioChange}>
                    <option>16:9</option>
                    <option>4:3</option>
                    <option>Custom</option>
                  </select>
                </div>
              </div>
              <div className="form-group">
                <label className={labelClass}>WxH</label>
                <div className={controlClass}>
                  <div className={`form-group ${formControlClass} ${whClass}`}>
                    <label className={labelClass} style={{ paddingTop: '0' }}>
                      Player Width
                    </label>
                    <div className={controlClass}>
                      <div className="input-group">
                        <input className={`form-control ${formControlClass}`} type="number" placeholder="auto" value={bcPlayerProps.playerWidth} onChange={handleWidthChange} />
                        <span className="input-group-addon">{bcPlayerProps.units || 'px'}</span>
                      </div>
                    </div>
                  </div>
                  <div className={`form-group ${formControlClass} ${whClass}`}>
                    <label className={labelClass} style={{ paddingTop: '0' }}>
                      Player Height
                    </label>
                    <div className={controlClass}>
                      <div className="input-group">
                        <input
                          className={`form-control ${formControlClass}`}
                          type="number"
                          placeholder="auto"
                          value={bcPlayerProps.playerHeight}
                          disabled={bcPlayerProps.aspectRatio !== BrightcovePlayerAspectRatio.Custom}
                          onChange={handleHeightChange}
                        />
                        <span className="input-group-addon">{bcPlayerProps.units || 'px'}</span>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </>
          )}
          {showUnitsCustomizations && (
            <div className="form-group">
              <label className={labelClass}>Units</label>
              <div className={controlClass}>
                <select className={`form-control ${formControlClass}`} value={bcPlayerProps.units} onChange={handleUnitsChange}>
                  <option>px</option>
                  <option>em</option>
                </select>
              </div>
            </div>
          )}
        </>
      )}
    </>
  );
};

export interface BrightcovePlayerControlsProps {
  defaultPlayerId?: string;
  playerType: 'Video' | 'Playlist';
  renderMode: 'Full' | 'Part' | 'Embed';
  retrieveAllPlayers: (accountId: string) => Promise<BCPlayer[]>;
  labelClass?: string;
  controlClass?: string;
  formControlClass?: string;
  showEmbedCustomizations: boolean;
  showAdvancedEmbedCustomizations: boolean;
  showUnitsCustomizations?: boolean;
  whClass?: string;
  bcPlayerProps: Partial<BrightcovePlayerProps>;
  onPlayerPropsChanged: (bcPlayerProps: Partial<BrightcovePlayerProps>) => void;
}
