import * as React from 'react';
import * as ReactDOMServer from 'react-dom/server';
import { Button } from 'react-bootstrap';
import { CopyToClipboard, Spinner } from '../common';
import autobind from 'autobind-decorator';

import { BrightcovePlayerProps, BrightcovePlayer } from '../brightcove-player';
import { BrightcovePlayerControls } from '../brightcove-player';
import { BrightcovePreview } from '../brightcove-preview';

import { SPUtility, SPAddinContext, SharePointApiGateway, SPEmbedUtil } from '../../services/sharepoint';
import { BCPlayer } from '@brightcove/video-cloud-sdk';

import * as FileSaver from 'file-saver';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faDownload, faUpload } from '@fortawesome/free-solid-svg-icons';
import moment from 'moment';
import { EmbedLayoutMode } from './EmbedLayoutMode';

export class VideoEmbed extends React.Component<MiniVideoEmbedProps, MiniVideoEmbedState> {
  constructor(props: MiniVideoEmbedProps, context?: any) {
    super(props, context);

    this.state = {
      isWebPartUploading: false
    };
  }

  @autobind
  handlePlayerPropsChanged(bcPlayerProps: Partial<BrightcovePlayerProps>) {
    const { onPlayerPropsChanged } = this.props;

    bcPlayerProps.embedCodeOverride = undefined;
    if (onPlayerPropsChanged) {
      onPlayerPropsChanged(bcPlayerProps);
    }
  }

  @autobind
  handleEmbedCodeChange(embedCodeInput: React.ChangeEvent<HTMLTextAreaElement>) {
    const { onPlayerPropsChanged, bcPlayerProps } = this.props;
    bcPlayerProps.embedCodeOverride = embedCodeInput.target.value;

    if (onPlayerPropsChanged) {
      onPlayerPropsChanged(bcPlayerProps);
    }
  }

  @autobind
  async addWebPartToPage() {
    const { bcPlayerProps, spApiGateway, spAddinContext } = this.props;

    if (!spApiGateway || !spAddinContext) {
      return;
    }

    const embedCode = bcPlayerProps.embedCodeOverride || this.generateVideoEmbedCode();
    await spApiGateway.spEmbedUtil.insertContentEditorWebPart(
      spAddinContext.Referrer,
      'Brightcove Video WebPart',
      'Brightcove Web Part to embed a video onto a web part page.',
      embedCode || 'An error occurred.'
    );
  }

  @autobind
  async takeSnapshot() {
    const embedElement = window.document.getElementById('bcEmbedPreview');
    if (!embedElement) {
      return;
    }

    const videoElement = embedElement.querySelector('video') as HTMLVideoElement;

    const newCanvasElement = document.createElement('canvas');
    newCanvasElement.width = videoElement.clientWidth;
    newCanvasElement.height = videoElement.clientHeight;

    const ctx = newCanvasElement.getContext('2d');
    if (ctx) {
      ctx.drawImage(videoElement, 0, 0, newCanvasElement.width, newCanvasElement.height);
      newCanvasElement.toBlob(blob => {
        if (!blob) {
          return;
        }
        FileSaver.saveAs(blob, 'imageData.png');
      });
    }
  }

  getWebPartTitle() {
    const { bcPlayerProps, videoTitle } = this.props;

    let webPartName = 'Brightcove Video WebPart';
    if (videoTitle) {
      let embedType = 'javascript';
      if (bcPlayerProps.embedType === 'Standard') {
        embedType = 'iframe';
      }

      webPartName = `${videoTitle}-${bcPlayerProps.accountId}-${bcPlayerProps.playerId}-${bcPlayerProps.modeId}-${embedType}`;
    }

    return webPartName;
  }

  @autobind
  async uploadWebPartToGallery() {
    const { bcPlayerProps, spApiGateway, spAddinContext } = this.props;

    if (!spApiGateway || !spAddinContext) {
      return;
    }

    this.setState({
      isWebPartUploading: true
    });

    const embedCode = bcPlayerProps.embedCodeOverride || this.generateVideoEmbedCode();
    const webPartTitle = this.getWebPartTitle();
    const webPart = SPEmbedUtil.generateScriptEditorWebPart(webPartTitle, 'Adds a Brightcove Video Embed onto classic and modern pages.', embedCode || 'An error occurred.');

    try {
      const dt = moment();
      const filename = `BrightcoveVideo-${dt.format('YYYY-MM-DD')}-${dt.format('hh-mm-ss')}.webpart`;
      await spApiGateway.spEmbedUtil.uploadWebPartToGallery(webPart, filename, 'Brightcove Videos');
    } catch (ex) {
      // Do Nothing.
    }

    SPUtility.closeParentDialog(true);
  }

  @autobind
  downloadScriptEditorWebPart() {
    const { bcPlayerProps } = this.props;

    const embedCode = bcPlayerProps.embedCodeOverride || this.generateVideoEmbedCode();
    const webPartTitle = this.getWebPartTitle();
    const webPart = SPEmbedUtil.generateScriptEditorWebPart(webPartTitle, 'Adds a Brightcove Video Embed onto classic and modern pages.', embedCode || 'An error occurred.');

    const dt = moment();
    const filename = `BrightcoveVideo-${dt.format('YYYY-MM-DD')}-${dt.format('hh-mm-ss')}.webpart`;
    FileSaver.saveAs(new Blob([webPart], { type: 'text/plain;charset=utf-8' }), filename);
  }

  generateVideoEmbedCode() {
    const { bcPlayerProps } = this.props;

    const embedCode = ReactDOMServer.renderToStaticMarkup(<BrightcovePlayer {...(bcPlayerProps as BrightcovePlayerProps)} mode="Video" />)
      .replace(/&amp;/g, '&')
      .replace(/%3A/g, ':');

    return embedCode;
  }

  renderMini(playerPreviewUrl: string, embedCode: string) {
    const { bcPlayerProps, defaultVideoPlayerId, retrieveAllPlayers, showUploadWebPartButton, doesEmbedHelperExist } = this.props;

    const { isWebPartUploading } = this.state;

    const labelClass = 'col-xs-3 control-label small';
    const controlClass = 'col-xs-9';
    bcPlayerProps.renderMode = 'Embed';

    return (
      <>
        <div className="form-group">
          <label className={labelClass}>Preview in browser</label>
          <div className={controlClass + ' word-wrap'}>
            <a href={playerPreviewUrl} target="_blank" rel="noopener noreferrer">
              {playerPreviewUrl}
            </a>
          </div>
        </div>
        <BrightcovePlayerControls
          retrieveAllPlayers={retrieveAllPlayers}
          defaultPlayerId={defaultVideoPlayerId}
          playerType="Video"
          renderMode="Embed"
          labelClass={labelClass}
          controlClass={controlClass}
          formControlClass="input-sm small"
          whClass="col-xs-6"
          showEmbedCustomizations={true}
          showAdvancedEmbedCustomizations={true}
          bcPlayerProps={bcPlayerProps}
          onPlayerPropsChanged={this.handlePlayerPropsChanged}
        />
        <div className="col-xs-12 small">
          <div>Brightcove Snippet (Paste to Script Editor web part or publishing zone):</div>
          <textarea id="embedCode" style={{ width: '100%', height: '65px', wordBreak: 'break-all' }} value={embedCode} spellCheck={false} onChange={this.handleEmbedCodeChange} />
          <CopyToClipboard />
          <Button className="pull-right" bsSize="small" style={{ margin: '2px' }} onClick={this.downloadScriptEditorWebPart}>
            <FontAwesomeIcon icon={faDownload} />
            &nbsp;Download Web Part
          </Button>
          {isWebPartUploading && <Spinner loadingText="Uploading..." />}
          {showUploadWebPartButton === true && !isWebPartUploading && (
            <Button
              className="pull-right"
              bsSize="small"
              style={{ margin: '2px' }}
              disabled={!doesEmbedHelperExist}
              onClick={this.uploadWebPartToGallery}
              title={doesEmbedHelperExist ? '' : 'Unable to upload web part as the Brightcove Embed Helper is not installed.'}
            >
              <FontAwesomeIcon icon={faUpload} />
              &nbsp;Upload Web Part to Gallery
            </Button>
          )}
        </div>
        <div className="col-xs-12 small" style={{ paddingTop: '5px' }}>
          <Button className="pull-right" bsSize="small" onClick={() => SPUtility.closeParentDialog(false)}>
            Close Dialog
          </Button>
        </div>
      </>
    );
  }

  renderPart(playerPreviewUrl: string, embedCode: string) {
    const { bcPlayerProps, defaultVideoPlayerId, retrieveAllPlayers } = this.props;

    const labelClass = 'col-xs-3 control-label small';
    const controlClass = 'col-xs-9';
    bcPlayerProps.renderMode = 'Part';

    return (
      <>
        <div className="form-group">
          <label className={labelClass}>Preview in browser</label>
          <div className={controlClass + ' word-wrap'}>
            <a href={playerPreviewUrl} target="_blank" rel="noopener noreferrer">
              {playerPreviewUrl}
            </a>
          </div>
        </div>
        <BrightcovePlayerControls
          retrieveAllPlayers={retrieveAllPlayers}
          defaultPlayerId={defaultVideoPlayerId}
          playerType="Video"
          renderMode="Part"
          labelClass={labelClass}
          controlClass={controlClass}
          formControlClass="input-sm small"
          whClass="col-xs-6"
          showEmbedCustomizations={true}
          showAdvancedEmbedCustomizations={false}
          bcPlayerProps={bcPlayerProps}
          onPlayerPropsChanged={this.handlePlayerPropsChanged}
        />
        <div className="col-xs-12" style={{ padding: '0' }}>
          <label>Brightcove Snippet (Edit to customize Playlist embed code)</label>
        </div>
        <div className="col-xs-12" style={{ padding: '0' }}>
          <textarea id="embedCode" style={{ width: '100%', height: '65px', wordBreak: 'break-all' }} value={embedCode} spellCheck={false} onChange={this.handleEmbedCodeChange} />
          <CopyToClipboard />
        </div>
      </>
    );
  }

  renderFull(playerPreviewUrl: string, embedCode: string) {
    const { bcPlayerProps, defaultVideoPlayerId, retrieveAllPlayers, spAddinContext } = this.props;

    bcPlayerProps.renderMode = 'Full';

    return (
      <>
        <div className="row">
          <div className="form-group">
            <div id="bcEmbedPreview">
              {bcPlayerProps.accountId && bcPlayerProps.modeId && spAddinContext && (
                <BrightcovePreview accountId={bcPlayerProps.accountId} playerId={bcPlayerProps.playerId} videoId={bcPlayerProps.modeId} style={{ width: '100%', height: '400px' }} />
              )}
            </div>
          </div>
          <div className="form-group">
            <label>Preview in browser</label>
            <div>
              <a href={playerPreviewUrl} target="_blank" rel="noopener noreferrer">
                {playerPreviewUrl}
              </a>
            </div>
          </div>
        </div>
        <div className="row">
          <div className="col-md-6">
            <h4>Copy Code</h4>
            <div>Copy the appropriate code below and paste it into your web page or app.</div>
            <textarea id="embedCode" style={{ width: '100%', height: '300px', wordBreak: 'break-all' }} value={embedCode} spellCheck={false} onChange={this.handleEmbedCodeChange} />
            <CopyToClipboard />
            <Button className="pull-right" bsSize="small" style={{ margin: '2px' }} onClick={this.downloadScriptEditorWebPart}>
              <FontAwesomeIcon icon={faDownload} /> Download Web Part
            </Button>
          </div>
          <div className="col-md-6">
            Change Player Size
            <BrightcovePlayerControls
              retrieveAllPlayers={retrieveAllPlayers}
              defaultPlayerId={defaultVideoPlayerId}
              playerType="Video"
              renderMode="Full"
              showEmbedCustomizations={true}
              showAdvancedEmbedCustomizations={false}
              bcPlayerProps={bcPlayerProps}
              onPlayerPropsChanged={this.handlePlayerPropsChanged}
            />
          </div>
        </div>
      </>
    );
  }

  render() {
    const { bcPlayerProps, layoutMode } = this.props;

    const playerPreviewUrl = `https://players.brightcove.net/${bcPlayerProps.accountId}/${bcPlayerProps.playerId}_default/index.html?videoId=${bcPlayerProps.modeId}`;
    const embedCode = bcPlayerProps.embedCodeOverride || this.generateVideoEmbedCode();

    return (
      <>
        {(() => {
          switch (layoutMode) {
            case 'Mini':
              return this.renderMini(playerPreviewUrl, embedCode);
            case 'Part':
              return this.renderPart(playerPreviewUrl, embedCode);
            default:
              return this.renderFull(playerPreviewUrl, embedCode);
          }
        })()}
      </>
    );
  }
}

export interface MiniVideoEmbedProps {
  bcPlayerProps: Partial<BrightcovePlayerProps>;
  videoTitle: string;
  defaultVideoPlayerId: string;
  layoutMode: EmbedLayoutMode;

  spAddinContext?: SPAddinContext;
  spApiGateway?: SharePointApiGateway;
  doesEmbedHelperExist?: boolean;
  showUploadWebPartButton?: boolean;

  retrieveAllPlayers: (accountId: string) => Promise<BCPlayer[]>;
  onPlayerPropsChanged?: (bcPlayerProps: Partial<BrightcovePlayerProps>) => void;
}

export interface MiniVideoEmbedState {
  isWebPartUploading: boolean;
}
