import * as React from 'react';
import { FormControl } from 'react-bootstrap';
import autobind from 'autobind-decorator';

import { BrightcoveApiGateway, BCIngestProfile, BCGetDefaultIngestProfileResponse } from '@brightcove/video-cloud-sdk';

import { find } from 'lodash';

export class IngestProfilePicker extends React.Component<IngestProfilePickerProps, IngestProfilePickerState> {
  constructor(props: IngestProfilePickerProps, context?: any) {
    super(props, context);

    this.state = {
      isLoadingIngestProfiles: true,
      allIngestProfiles: []
    };
  }

  async UNSAFE_componentWillMount() {
    if (this.props.accountId) {
      this.getIngestProfiles(this.props);
    }
  }

  async UNSAFE_componentWillReceiveProps(nextProps: IngestProfilePickerProps) {
    const { accountId } = nextProps;
    const { allIngestProfiles } = this.state;

    if (accountId && nextProps.accountId !== this.props.accountId && allIngestProfiles.length === 0) {
      await this.getIngestProfiles(nextProps);
    }
  }

  @autobind
  async getIngestProfiles(props: IngestProfilePickerProps) {
    const { accountId, bcApiGateway, onChange } = props;
    const { ingestProfilesApi } = bcApiGateway;

    if (!accountId) {
      return;
    }

    this.setState({
      isLoadingIngestProfiles: true
    });

    const defaultIngestProfile = await ingestProfilesApi.getDefaultProfile(accountId);

    const allIngestProfiles = await ingestProfilesApi.getAllIngestProfiles(accountId);

    // map the profiles to a hash.

    let selectedIngestProfile: BCIngestProfile | undefined = undefined;
    if (allIngestProfiles && allIngestProfiles.length > 0) {
      if (defaultIngestProfile) {
        selectedIngestProfile = find(allIngestProfiles, { id: defaultIngestProfile.id });
      }

      if (!selectedIngestProfile) {
        selectedIngestProfile = allIngestProfiles[0];
      }

      if (selectedIngestProfile && onChange) {
        onChange(selectedIngestProfile);
      }
    }

    // Sort the players alphabetically by namewith the default player on top.

    this.setState({
      isLoadingIngestProfiles: false,
      selectedIngestProfile
    });
  }

  @autobind
  handlePlayerChange(event: React.FormEvent<FormControl>) {
    const { onChange } = this.props;
    const { allIngestProfiles } = this.state;
    const target = event.target as any;

    const selectedIngestProfile = find(allIngestProfiles, { id: target.value });

    if (selectedIngestProfile && onChange) {
      onChange(selectedIngestProfile);
    }
  }

  render() {
    const { placeholder, className, accountId, selectedIngestProfileId } = this.props;
    const { isLoadingIngestProfiles, allIngestProfiles, defaultIngestProfile } = this.state;

    if (!accountId) {
      return (
        <FormControl className={className} componentClass="select" disabled={true}>
          <option>Please select an account...</option>
        </FormControl>
      );
    }

    if (isLoadingIngestProfiles) {
      return (
        <FormControl className={className} componentClass="select" disabled={true}>
          <option>Loading Ingest Profiles...</option>
        </FormControl>
      );
    }

    let bcPlaceholder = placeholder;
    if (!bcPlaceholder) {
      bcPlaceholder = 'Select an ingest profile...';
    }

    let bcIngestProfileId = selectedIngestProfileId;
    if (!bcIngestProfileId && defaultIngestProfile) {
      bcIngestProfileId = defaultIngestProfile.id;
    }

    return (
      <FormControl className={className} componentClass="select" placeholder={bcPlaceholder} value={bcIngestProfileId} onChange={this.handlePlayerChange}>
        {allIngestProfiles.map((ingestProfile, i) => (
          <option key={i} value={ingestProfile.id}>
            {ingestProfile.name}
          </option>
        ))}
      </FormControl>
    );
  }
}

export interface IngestProfilePickerProps {
  className?: string;
  placeholder?: string;
  bcApiGateway: BrightcoveApiGateway;
  accountId?: string;
  selectedIngestProfileId?: string;
  onChange?: (selectedIngestProfile: BCIngestProfile) => void;
}

export interface IngestProfilePickerState {
  isLoadingIngestProfiles: boolean;
  defaultIngestProfile?: BCGetDefaultIngestProfileResponse;
  allIngestProfiles: Array<BCIngestProfile>;
  ingestProfilesObj?: Array<BCIngestProfile>;
  selectedIngestProfile?: BCIngestProfile;
}
