import * as React from 'react';
import { Link } from 'react-router-dom';
import { Button, Modal } from 'react-bootstrap';
import { BootstrapTable, TableHeaderColumn, Options } from 'react-bootstrap-table';
import { AccountPicker, Spinner } from '../common';
import { ExperienceEmbed, EmbedLayoutMode } from '../embeds';

import { BrightcoveAccount } from 'services';
import { BrightcoveApiGateway, BCExperience } from '@brightcove/video-cloud-sdk';
import { SharePointApiGateway } from 'services/sharepoint';
import { ConnectorContext } from 'services/connector';
import { BrightcoveExperienceProps, BrightcoveExperienceUtils } from '../brightcove-experience';

import autobind from 'autobind-decorator';
import './ManageExperiences.css';

export class ManageExperiences extends React.Component<ManageExperiencesProps, ManageExperiencesState> {
  constructor(props: ManageExperiencesProps, context?: any) {
    super(props, context);

    this.state = {
      isLoadingExperiences: true,
      showEmbedModal: false,
      experiences: []
    };
  }

  @autobind
  UNSAFE_componentWillMount() {
    this.loadExperiences(this.props.accountId);
  }

  @autobind
  async UNSAFE_componentWillUpdate(nextProps: ManageExperiencesProps) {
    if (nextProps.accountId !== this.props.accountId) {
      this.loadExperiences(nextProps.accountId);
    }
  }

  async loadExperiences(accountId: string) {
    if (!accountId) {
      return;
    }

    this.setState({
      isLoadingExperiences: true
    });

    const experiencesApi = this.props.bcApiGateway.experiencesApi;
    try {
      const experiencesResponse = await experiencesApi.getExperiences(accountId);

      this.setState({
        isLoadingExperiences: false,
        experiences: experiencesResponse.items
      });
    } catch (err) {
      this.setState({
        isLoadingExperiences: false,
        experiences: []
      });
    }
  }

  @autobind
  async handleAccountChange(newAccount: BrightcoveAccount) {
    const { onAccountChange } = this.props;

    if (onAccountChange) {
      onAccountChange(newAccount.accountId);
    }

    this.loadExperiences(newAccount.accountId);
  }

  @autobind
  handleEmbedPlayerPropsChanged(embedExperienceProps: Partial<BrightcoveExperienceProps>) {
    this.setState({
      embedExperienceProps
    });
  }

  @autobind
  showEmbedModal(experience: BCExperience) {
    const { accountId } = this.props;

    const embedExperienceProps = BrightcoveExperienceUtils.getDefaultBrightcoveExperienceProps();
    embedExperienceProps.accountId = accountId;
    embedExperienceProps.experienceId = experience.id;
    embedExperienceProps.sharepointVersion = this.props.connectorContext.spAddinContext.SPProductNumber;

    this.setState({
      showEmbedModal: true,
      embedExperience: experience,
      embedExperienceProps
    });
  }

  @autobind
  hideEmbedModal() {
    this.setState({
      showEmbedModal: false,
      embedExperience: undefined,
      embedExperienceProps: undefined
    });
  }

  @autobind
  renderExperienceNameCell(cell: any, experience: BCExperience) {
    const { accountId } = this.props;
    const editPlaylistUrl = `/${accountId}/manage-experiences/${experience.id}/preview`;
    return (
      <>
        <Link to={editPlaylistUrl}>{experience.name}</Link>
        <br />
        {experience.videos.videoIds && experience.videos.videoIds.length > 0 && (
          <>
            <div style={{ fontSize: 'small' }}>Video IDs in list: </div>
            <div style={{ fontSize: 'xx-small' }}>
              {experience.videos.videoIds.map(videoId => (
                <span key={videoId} style={{ paddingLeft: '5px', float: 'left' }}>
                  {videoId}
                </span>
              ))}
            </div>
          </>
        )}
        {(!experience.videos.videoIds || experience.videos.videoIds.length === 0) && <i>No Videos</i>}
      </>
    );
  }

  @autobind
  renderEditLink(cell: any, experience: BCExperience): React.ReactElement<any> {
    // const { accountId } = this.props;
    // const editPlaylistUrl = `/${accountId}/manage-experiences/${experience.id}/preview`;;

    return (
      <div className="result text-center">
        {/* <div>
                    <Link
                        style={{ width: '70px' }}
                        to={editPlaylistUrl}
                    >
                        Preview
                    </Link>
                </div> */}
        <div>
          <Button bsStyle="success" style={{ width: '70px' }} bsSize="small" onClick={() => this.showEmbedModal(experience)}>
            Embed
          </Button>
        </div>
      </div>
    );
  }

  render() {
    const { accountId, connectorContext } = this.props;

    const { isLoadingExperiences, experiences, showEmbedModal, embedExperience, embedExperienceProps } = this.state;

    const experienceTableOptions: Options = {
      defaultSortName: 'updatedAt', // default sort column name
      defaultSortOrder: 'desc', // default sort order
      noDataText: 'There are no experiences defined in this account.'
    };

    const labelClass = 'col-md-3 control-label';
    const controlClass = 'col-md-9';

    return (
      <>
        <ol className="breadcrumb">
          <li>
            <Link to="/">Brightcove Management</Link>
          </li>
          <li className="active">Manage Experiences</li>
        </ol>
        <div className="search form-horizontal">
          <div className="form-group">
            <label className={labelClass}>Select Account:</label>{' '}
            <div className={controlClass}>
              <AccountPicker availableAccounts={connectorContext.availableAccounts} selectedAccountId={accountId} onChange={this.handleAccountChange} />
            </div>
          </div>
        </div>
        {accountId && (
          <>
            {isLoadingExperiences && <Spinner loadingText="Loading Experiences..." />}
            {!isLoadingExperiences && (
              <>
                <BootstrapTable data={experiences} striped={true} bordered={false} options={experienceTableOptions}>
                  <TableHeaderColumn dataField="name" dataFormat={this.renderExperienceNameCell} dataSort={true}>
                    Name
                  </TableHeaderColumn>
                  <TableHeaderColumn dataField="description" dataSort={true}>
                    Short Description
                  </TableHeaderColumn>
                  <TableHeaderColumn dataField="template" dataSort={true}>
                    Type
                  </TableHeaderColumn>
                  <TableHeaderColumn width="200" dataField="id" dataSort={true} isKey={true}>
                    ID
                  </TableHeaderColumn>
                  <TableHeaderColumn dataField="updatedAt" dataSort={true} hidden={true}>
                    Updated At
                  </TableHeaderColumn>
                  <TableHeaderColumn width="90" dataFormat={this.renderEditLink}>
                    Edit Link
                  </TableHeaderColumn>
                </BootstrapTable>
                <hr />
              </>
            )}
            <Modal className="embed" show={showEmbedModal} onHide={this.hideEmbedModal} bsSize="large">
              <Modal.Header closeButton={true}>
                <Modal.Title>Embed Experience</Modal.Title>
              </Modal.Header>
              <Modal.Body>
                {embedExperienceProps && (
                  <ExperienceEmbed
                    bcExperienceProps={embedExperienceProps}
                    layoutMode={EmbedLayoutMode.Full}
                    connectorContext={connectorContext}
                    experience={embedExperience}
                    onExperiencePropsChanged={this.handleEmbedPlayerPropsChanged}
                  />
                )}
              </Modal.Body>
              <Modal.Footer />
            </Modal>
          </>
        )}
      </>
    );
  }
}

interface ManageExperiencesProps {
  accountId: string;
  connectorContext: ConnectorContext;
  bcApiGateway: BrightcoveApiGateway;
  spApiGateway: SharePointApiGateway;
  onAccountChange?: (accountId: string) => void;
}

interface ManageExperiencesState {
  isLoadingExperiences: boolean;
  experiences: Array<BCExperience>;

  showEmbedModal: boolean;
  embedExperience?: BCExperience;
  embedExperienceProps?: Partial<BrightcoveExperienceProps>;
}
