import * as React from 'react';
import { Button } from 'react-bootstrap';
import Datetime, { EventOrValueHandler } from 'react-datetime';

export class DatePicker extends React.Component<DatePickerProps> {
  renderDateTime(props: any, openCalendar: React.MouseEventHandler<Button>): JSX.Element {
    return (
      <div className="input-group">
        <input className="form-control" {...props} />
        <div className="input-group-btn">
          <Button onClick={openCalendar} disabled={props.disabled}>
            &nbsp;
            <i className="glyphicon glyphicon-calendar" />
          </Button>
        </div>
      </div>
    );
  }

  render() {
    const { value, placeholder, onChange, disabled } = this.props;

    return <Datetime value={value} onChange={onChange} timeFormat={false} renderInput={this.renderDateTime} inputProps={{ placeholder: placeholder, disabled: disabled }} />;
  }
}

export interface DatePickerProps {
  name?: string;
  value?: Date;
  placeholder?: string;
  onChange?: EventOrValueHandler<React.ChangeEvent<any>>;
  disabled?: boolean;
}
