import { Web } from 'sp-pnp-js';
import { BrightcoveSettings, defaultBrightcoveSettings } from '../BrightcoveSettings';

import { defaultsDeep } from 'lodash';
import URI from 'urijs';
import * as CryptoJS from 'crypto-js';

const BrightcoveSettingsDocumentLibraryName = 'BrightcoveSettings';
const BrightcoveSettingsFileName = 'ConnectorSettings.txt';
const BrightcoveAccountSecretPassphrase = 'BrightcoveConectorRocks';

export class BrightcoveSettingsManager {
  private readonly _addinWebUrl?: string;
  private readonly _hostWebUrl: string;
  private readonly _webUrl: string;
  private readonly _web: Web;

  private readonly _errorCallback?: (message: string, err?: Error) => boolean | undefined;

  constructor(hostWebUrl: string, addinWebUrl?: string, errorCallback?: (message: string, err?: Error) => boolean | undefined) {
    this._hostWebUrl = hostWebUrl;
    this._addinWebUrl = addinWebUrl;

    if (!addinWebUrl) {
      this._webUrl = hostWebUrl;
      this._web = new Web(hostWebUrl);
    } else {
      this._webUrl = addinWebUrl;
      this._web = new Web(addinWebUrl);
    }

    if (process.env.NODE_ENV === 'development' && URI(this._webUrl).domain() === 'localhost') {
      this._webUrl += URI(hostWebUrl).path() + '/bcvc4sp';
    }

    this._errorCallback = errorCallback;
  }

  public get addinWebUrl(): string | undefined {
    return this._addinWebUrl;
  }

  public get hostWebUrl(): string {
    return this._hostWebUrl;
  }

  public get web(): Web {
    return this._web;
  }

  public async loadBrightcoveSettings(decryptSecrets?: boolean): Promise<BrightcoveSettings> {
    // const settingsFileServerRelativeUrl = URI(`${this._webUrl}/${BrightcoveSettingsDocumentLibraryName}/${BrightcoveSettingsFileName}`).path();

    let settings: BrightcoveSettings = defaultBrightcoveSettings;
    try {
      const savedSettings: any = {}; // await this._web.getFileByServerRelativeUrl(settingsFileServerRelativeUrl).getJSON();
      if (!savedSettings.error) {
        settings = defaultsDeep(savedSettings, defaultBrightcoveSettings);
      }
    } catch (ex) {
      // Do Nothing
    }

    // Decrypt account secrets
    if (decryptSecrets === undefined || decryptSecrets === true) {
      // for (const account of settings.brightcoveAccounts) {
      //   if (account.clientSecret) {
      //     try {
      //       account.clientSecret = CryptoJS.AES.decrypt(account.clientSecret, BrightcoveAccountSecretPassphrase).toString(CryptoJS.enc.Utf8);
      //     } catch (err) {
      //       this.raiseError(`Error decrypting client secret for ${account.accountId}`);
      //     }
      //   }
      // }
    }

    return settings;
  }

  public async saveBrightcoveSettings(settings: BrightcoveSettings, encryptSecrets?: boolean): Promise<void> {
    const settingsPath = URI(`${this._webUrl}/${BrightcoveSettingsDocumentLibraryName}/`).path();

    if (encryptSecrets === undefined || encryptSecrets === true) {
      for (const account of settings.brightcoveAccounts) {
        const encrypedSecret = CryptoJS.AES.encrypt(account.clientSecret, BrightcoveAccountSecretPassphrase).toString();
        account.clientSecret = encrypedSecret;
      }
    }

    try {
      this._web.getFolderByServerRelativeUrl(settingsPath).files.add(BrightcoveSettingsFileName, JSON.stringify(settings), true);

      // Wait for a few seconds
      const promise = new Promise(resolve => setTimeout(resolve, 2000));
      await promise;
    } catch (ex) {
      const err = this.raiseError(`Unable to save Brightcove Settings`);
      throw err;
    }
  }

  /**
   * Raises an error to a callback optionally specified in the constructor.
   */
  protected raiseError(message: string, err?: Error): Error {
    const errorMessage = `${message} ${err && err.message ? err.message : ''}`;
    const outerErr = new Error(errorMessage);

    if (this._errorCallback) {
      const throwException = this._errorCallback(message, err);
      if (throwException === true) {
        throw outerErr;
      }
    } else {
      throw outerErr;
    }

    return outerErr;
  }
}
