import * as React from 'react';

export const LoadingProgress = (props: LoadingProgressProps) => {
  let { loadingMessage } = props;
  if (!loadingMessage) {
    loadingMessage = 'Loading Brightcove Video Connector for SharePoint...';
  }

  return (
    <div className="loading-progress">
      <div className="loading-progress-content">
        <div className="loading-logo">
          <img src="images/brightcove-logo-vertical-grey.png" alt="logo" />
        </div>
        <div className="loading-progress-bar" role="progressbar">
          <div className="progressContainer">
            <div className="primaryProgress" />
          </div>
        </div>
        <div className="loading-progress-message">{loadingMessage}</div>
      </div>
    </div>
  );
};

export interface LoadingProgressProps {
  loadingMessage?: string;
}
