import * as React from 'react';
import { Link } from 'react-router-dom';
import { Prompt } from 'react-router';
import { Button, Modal } from 'react-bootstrap';
import Dropzone, { ImageFile } from 'react-dropzone';
import { AccountPicker, DatePicker, Spinner } from '../common';
import { EditVideoTextTracks, ActiveTextTrack } from './';
import { BrightcovePreview } from '../brightcove-preview';

import { BCVideo, BCCustomField, BCVideoTextTrack } from '@brightcove/video-cloud-sdk';
import { ConnectorContext, mapBCVideoToMVideo, mapMVideoToBCVideo, ConnectorVideo } from '../../services/connector';

import autobind from 'autobind-decorator';
import * as moment from 'moment';
import { filter, set, cloneDeep, isEqual, pick, startsWith } from 'lodash';

import './EditVideo.css';

export class EditVideo extends React.Component<EditVideoProps, EditVideoState> {
  constructor(props: EditVideoProps, context?: any) {
    super(props, context);

    this.state = {
      isLoadingVideo: true,
      isSavingVideo: false,
      savingVideoStatus: '',
      showBrightcoveProcessingModal: false
    };
  }

  @autobind
  async componentDidMount() {
    await this.getVideo();
  }

  @autobind
  async getVideo() {
    const { accountId, videoId } = this.props;

    if (!videoId) {
      window.location.hash = `#/${accountId}/manage-videos`;
      return;
    }

    this.setState({
      isLoadingVideo: true
    });

    const videoInfo: { bcVideo: BCVideo; video: ConnectorVideo; customFields: Array<BCCustomField> } = await this.getBrightcoveVideo(videoId);

    this.setState({
      isLoadingVideo: false,
      video: cloneDeep(videoInfo.video),
      originalVideo: videoInfo.video,
      bcVideo: videoInfo.bcVideo,
      customFields: videoInfo.customFields
    });
  }

  @autobind
  async getBrightcoveVideo(videoId: string): Promise<{ bcVideo: BCVideo; video: ConnectorVideo; customFields: Array<BCCustomField> }> {
    const { accountId, connectorContext } = this.props;
    const { bcApiGateway } = connectorContext;
    const videosApi = bcApiGateway.videosApi;
    const bcVideo = await videosApi.getVideoById(accountId, videoId);

    const video = mapBCVideoToMVideo(bcVideo);

    // Get custom field definitions too
    const customFields = await videosApi.getCustomFields(accountId);

    return {
      bcVideo,
      video,
      customFields: customFields.custom_fields
    };
  }

  @autobind
  addVideoTextTrack(textTrack: ActiveTextTrack) {
    const { video } = this.state;

    if (!video) {
      return;
    }

    if (!video.textTracks) {
      video.textTracks = [];
    }

    video.textTracks.push(textTrack);
    this.setState({
      video
    });
  }

  @autobind
  removeVideoTextTrack(textTrack: ActiveTextTrack) {
    const { video } = this.state;

    if (!video) {
      return;
    }

    if (video.textTracks) {
      const ix = video.textTracks.indexOf(textTrack);
      if (ix < 0) {
        return;
      }

      video.textTracks.splice(ix, 1);
      this.setState({
        video
      });
    }
  }

  @autobind
  setVideoTextTrackAsDefault(textTrack: Partial<BCVideoTextTrack>, checked: boolean) {
    const { video } = this.state;

    if (!video || !video.textTracks) {
      return;
    }

    video.textTracks.forEach(tt => {
      if (tt === textTrack && checked) {
        tt.default = true;
      } else {
        tt.default = false;
      }
    });

    this.setState({
      video
    });
  }

  @autobind
  handleCaptureCompleted() {
    this.setState({
      showBrightcoveProcessingModal: true
    });
  }

  @autobind
  async updateVideo() {
    const { accountId, videoId, connectorContext } = this.props;
    const { bcApiGateway } = connectorContext;
    const { video, bcVideo, newPosterImage, newThumbnailImage } = this.state;

    if (!video || !bcVideo || !accountId || !videoId) {
      return;
    }

    this.setState({
      isSavingVideo: true
    });

    const target = mapMVideoToBCVideo(video, filter(bcVideo.tags, t => startsWith(t, 'bcsp-')));
    if (target.text_tracks && !isEqual(target.text_tracks, bcVideo.text_tracks)) {
      const textTracks = [];
      for (const tt of target.text_tracks) {
        textTracks.push(pick(tt, ['id', 'account_id', 'srclang', 'label', 'kind', 'mime_type', 'asset_id', 'in_band_metadata_track_dispatch_type', 'default']) as BCVideoTextTrack);
      }
      // eslint-disable-next-line @typescript-eslint/camelcase
      target.text_tracks = textTracks;
    }

    const dynamicIngestApi = bcApiGateway.dynamicIngestApi;
    const videosApi = bcApiGateway.videosApi;

    this.setState({
      savingVideoStatus: 'Updating Brightcove Video...'
    });

    const bcUpdatedVideo = await videosApi.updateVideo(accountId, videoId, target);

    const updatedVideo = mapBCVideoToMVideo(bcUpdatedVideo);

    // Perform the secondary step to add new text tracks via the injest api.
    // See: https://support.brightcove.com/using-cms-api-add-webvtt-captions#replaceTracks
    const newTextTracks = filter(video.textTracks, tt => !tt.id);
    // For each new text track that has a 'file' property, upload to ingest location.
    const newFileTextTracks = filter(newTextTracks as Array<ActiveTextTrack>, tt => {
      if (tt.file) {
        return true;
      }
      return false;
    });

    if (newFileTextTracks.length > 0) {
      this.setState({
        savingVideoStatus: 'Uploading new text tracks...'
      });

      for (const newFileTextTrack of newFileTextTracks) {
        if (newFileTextTrack.file) {
          const uploadUrls = await dynamicIngestApi.getUploadUrls(accountId, videoId);
          await fetch(uploadUrls.signed_url, {
            method: 'PUT',
            credentials: 'omit',
            headers: {
              'Content-Type': ''
            },
            body: newFileTextTrack.file
          });

          newFileTextTrack.src = uploadUrls.api_request_url;
        }
      }
    }

    if (newTextTracks.length > 0) {
      this.setState({
        savingVideoStatus: 'Performing text track ingest request...'
      });
      await bcApiGateway.dynamicIngestApi.addTextTracks(accountId, videoId, newTextTracks);
    }

    // Perform the necessary steps to add new poster/thumbnail images.
    const newImageUrls: any = {
      posterUrl: undefined,
      thumbnailUrl: undefined
    };

    if (newPosterImage) {
      this.setState({
        savingVideoStatus: 'Uploading new Poster Image...'
      });
      const uploadUrls = await dynamicIngestApi.getUploadUrls(accountId, videoId, newPosterImage.name);
      newImageUrls.posterUrl = uploadUrls.api_request_url;
      await fetch(uploadUrls.signed_url, {
        method: 'PUT',
        credentials: 'omit',
        headers: {
          'Content-Type': ''
        },
        body: newPosterImage
      });
    }

    if (newThumbnailImage) {
      this.setState({
        savingVideoStatus: 'Uploading new Thumbnail...'
      });
      const uploadUrls = await dynamicIngestApi.getUploadUrls(accountId, videoId, newThumbnailImage.name);
      newImageUrls.thumbnailUrl = uploadUrls.api_request_url;
      await fetch(uploadUrls.signed_url, {
        method: 'PUT',
        credentials: 'omit',
        headers: {
          'Content-Type': ''
        },
        body: newThumbnailImage
      });
    }

    if (newPosterImage || newThumbnailImage) {
      this.setState({
        savingVideoStatus: 'Performing image ingest request...'
      });
      await bcApiGateway.dynamicIngestApi.addCustomImages(accountId, videoId, newImageUrls.posterUrl, newImageUrls.thumbnailUrl);
    }

    if (newPosterImage || newThumbnailImage || (newTextTracks && newTextTracks.length > 0)) {
      this.setState({
        showBrightcoveProcessingModal: true
      });
    }

    this.setState({
      isSavingVideo: false,
      video: cloneDeep(updatedVideo),
      originalVideo: updatedVideo,
      bcVideo: bcUpdatedVideo,
      newPosterImage: undefined,
      newThumbnailImage: undefined,
      savingVideoStatus: ''
    });
  }

  @autobind
  handleSelectChange(event: React.ChangeEvent<HTMLSelectElement>) {
    const video = this.state.video;

    if (!video) {
      return;
    }

    const target = event.target;
    const { value, name } = target;

    set(video, name, value);

    this.setState({
      video
    });
  }

  @autobind
  handleDateChange(name: string, value: string | React.ChangeEvent<any> | moment.Moment) {
    const video = this.state.video;

    if (!video) {
      return;
    }

    const momentValue = value as moment.Moment;
    if (momentValue) {
      set(video, name, momentValue.toDate());
    } else {
      set(video, name, null);
    }

    this.setState({
      video
    });
  }

  @autobind
  handleTextAreaChange(event: React.ChangeEvent<HTMLTextAreaElement>) {
    const video = this.state.video;

    if (!video) {
      return;
    }

    const target = event.target;
    const { value, name } = target;

    set(video, name, value);

    this.setState({
      video
    });
  }

  @autobind
  handleInputChange(event: React.ChangeEvent<HTMLInputElement>) {
    const video = this.state.video;

    if (!video) {
      return;
    }

    const target = event.target;
    const value = target.type === 'checkbox' ? target.checked : target.value;
    const name = target.name;

    switch (name) {
      case 'tags':
        video.tags = value
          .toString()
          .split(',')
          .map(s => s.trim());
        break;
      default:
        set(video, name, value);
        break;
    }

    this.setState({
      video
    });
  }

  @autobind
  handleDropPosterImage(acceptedFiles: ImageFile[]) {
    if (acceptedFiles.length === 1) {
      this.setState({
        newPosterImage: acceptedFiles[0]
      });
    }
  }

  @autobind
  handleDropThumbnail(acceptedFiles: ImageFile[]) {
    if (acceptedFiles.length === 1) {
      this.setState({
        newThumbnailImage: acceptedFiles[0]
      });
    }
  }

  @autobind
  hideBrightcoveProcessingModal() {
    this.setState({
      showBrightcoveProcessingModal: false
    });
  }

  render() {
    const { accountId, videoId, connectorContext } = this.props;
    const { isLoadingVideo, isSavingVideo, savingVideoStatus, video, originalVideo, customFields, newPosterImage, newThumbnailImage, showBrightcoveProcessingModal } = this.state;

    if (isSavingVideo) {
      return (
        <>
          <Spinner loadingText={savingVideoStatus ? savingVideoStatus : 'Saving Video...'} />
        </>
      );
    }

    if (isLoadingVideo || !video || !originalVideo) {
      return <Spinner loadingText="Loading Video..." />;
    }

    const labelClass = 'col-md-3 control-label';
    const controlClass = 'col-md-9';
    let posterDropzoneRef: Dropzone | null;
    let thumbnailDropzoneRef: Dropzone | null;

    const isSaveDisabled = !!videoId && isEqual(video, originalVideo) && !newPosterImage && !newThumbnailImage;

    // Do a little validation.
    const validationErrors = [];
    if (!video.name) {
      validationErrors.push('The name of the video is required.');
    }

    if (customFields) {
      for (const customField of customFields) {
        if (customField.required === true && (!video.customFields[customField.id] || video.customFields[customField.id].length === 0)) {
          validationErrors.push(`The '${customField.display_name}' custom field is marked as required and a value must be provided.`);
        }
      }
    }

    return (
      <>
        <ol className="breadcrumb">
          <li>
            <Link to="/">Brightcove Management</Link>
          </li>
          <li>
            <Link to={`/${accountId}/manage-videos`}>Manage Videos</Link>
          </li>
          <li>
            <Link to={`/${accountId}/manage-videos`}>Account {accountId}</Link>
          </li>
          <li className="active">Video {videoId}</li>
        </ol>
        <div className="form-horizontal">
          {!video.complete && (
            <div className="form-group">
              <div className="alert alert-warning" role="alert">
                This video is still processing on the Video Cloud. Renditions may not be available. This might also be a Live event with no thumbnail image.
              </div>
            </div>
          )}
          <div className="form-group">
            <label className={labelClass}>Select Account</label>{' '}
            <div className={controlClass}>
              <AccountPicker availableAccounts={connectorContext.availableAccounts} selectedAccountId={accountId} disabled={true} />
            </div>
          </div>

          <div className="form-group">
            <label className={labelClass}>Reference ID</label>
            <div className={controlClass}>
              <input className="form-control" disabled={true} value={video.referenceId} />
            </div>
          </div>

          <div className="form-group">
            <label className={`${labelClass} required`}>Name</label>
            <div className={controlClass}>
              <input className="form-control validate" type="text" placeholder="The name of the video" name="name" value={video.name} onChange={this.handleInputChange} />
            </div>
          </div>

          <div className="form-group">
            <label className={labelClass}>Short Description</label>
            <div className={controlClass}>
              <input className="form-control" type="text" placeholder="Short description here..." name="shortDescription" value={video.shortDescription} onChange={this.handleInputChange} />
            </div>
          </div>

          <div className="form-group">
            <label className={labelClass}>Long Description</label>
            <div className={controlClass}>
              <textarea className="form-control" placeholder="Long description here..." name="longDescription" value={video.longDescription} onChange={this.handleTextAreaChange} />
            </div>
          </div>

          <div className="form-group">
            <label className={labelClass}>Is Active</label>
            <div className={controlClass}>
              <select className="form-control" name="state" value={video.state} onChange={this.handleSelectChange}>
                <option value="ACTIVE">Active</option>
                <option value="INACTIVE">Inactive</option>
              </select>
            </div>
          </div>

          <div className="form-group">
            <label className={labelClass}>Related Link URL</label>
            <div className={controlClass}>
              <input className="form-control" type="text" name="relatedLinkUrl" value={video.relatedLinkUrl} onChange={this.handleInputChange} />
            </div>
          </div>

          <div className="form-group">
            <label className={labelClass}>Related Link Text</label>
            <div className={controlClass}>
              <input className="form-control" type="text" name="relatedLinkText" value={video.relatedLinkText} onChange={this.handleInputChange} />
            </div>
          </div>

          <div className="form-group">
            <label className={labelClass}>Start Availability Date</label>
            <div className={controlClass}>
              <DatePicker value={video.startAvailabilityDate} name="startAvailabilityDate" onChange={e => this.handleDateChange('startAvailabilityDate', e)} />
            </div>
          </div>

          <div className="form-group">
            <label className={labelClass}>End Availability Date</label>
            <div className={controlClass}>
              <DatePicker value={video.endAvailabilityDate} name="endAvailabilityDate" onChange={e => this.handleDateChange('endAvailabilityDate', e)} />
            </div>
          </div>

          <div className="form-group">
            <label className={labelClass}>Economics</label>
            <div className={controlClass}>
              <select className="form-control" name="economics" value={video.economics} onChange={this.handleSelectChange}>
                <option value="FREE">Free</option>
                <option value="AD_SUPPORTED">Ad Supported</option>
              </select>
            </div>
          </div>

          <div className="form-group">
            <label className={labelClass}> Brightcove Tags (comma-separated)</label>
            <div className={controlClass}>
              <input className="form-control" value={video.tags.join(', ')} name="tags" onChange={this.handleInputChange} />
            </div>
          </div>
        </div>

        <br />
        {customFields && (
          <>
            <h4>
              <strong>Custom Fields</strong>
            </h4>
            <hr />
            <div className="form-horizontal">
              {customFields.map(customField => (
                <div className="form-group" key={customField.id}>
                  <label className={`${labelClass}${customField.required ? ' required' : ''}`}>{customField.display_name}</label>
                  <div className={controlClass}>
                    {customField.type === 'string' && (
                      <input
                        className="form-control"
                        required={customField.required}
                        value={video.customFields[customField.id] || ''}
                        name={`customFields.${customField.id}`}
                        onChange={this.handleInputChange}
                      />
                    )}
                    {customField.type === 'enum' && (
                      <select
                        className="form-control"
                        required={customField.required}
                        value={video.customFields[customField.id] || ''}
                        name={`customFields.${customField.id}`}
                        onChange={this.handleSelectChange}
                      >
                        <option value="" />
                        {customField.enum_values.map((possibleValue, i) => (
                          <option key={i}>{possibleValue}</option>
                        ))}
                      </select>
                    )}
                    <small className="form-text text-muted">{customField.description}</small>
                  </div>
                </div>
              ))}
            </div>
            <br />
          </>
        )}

        <EditVideoTextTracks video={video} onAddTextTrack={this.addVideoTextTrack} onRemoveTextTrack={this.removeVideoTextTrack} onDefaultTextTrackChanged={this.setVideoTextTrackAsDefault} />

        <h4>
          <strong>Image Details</strong>
        </h4>
        <hr />
        <div className="form-group">
          <BrightcovePreview
            accountId={accountId}
            videoId={video.videoId}
            style={{ width: '100%', height: '400px' }}
            showToolbar={true}
            bcApiGateway={connectorContext.bcApiGateway}
            onCaptureComplete={this.handleCaptureCompleted}
          />
        </div>
        <div className="form-horizontal">
          <div className="form-group">
            <label className={labelClass}>Poster</label>
            <div className={controlClass}>
              <img className="col-md-12" src={video.videoStillUrl} alt="" />
              {video.videoStillUrl && (
                <Dropzone
                  ref={(node: Dropzone) => {
                    posterDropzoneRef = node;
                  }}
                  onDrop={this.handleDropPosterImage}
                  disableClick={true}
                  multiple={false}
                  accept="image/jpeg, image/png"
                >
                  {() => (
                    <div style={{ textAlign: 'right' }}>
                      <Button
                        bsStyle={newPosterImage ? 'success' : 'primary'}
                        bsSize="small"
                        onClick={() => {
                          posterDropzoneRef && posterDropzoneRef.open();
                        }}
                        style={{ marginTop: '10px' }}
                      >
                        {newPosterImage ? newPosterImage.name : 'Upload a new poster image'}
                      </Button>
                    </div>
                  )}
                </Dropzone>
              )}
            </div>
          </div>

          <div className="form-group">
            <label className={labelClass}>Thumbnail</label>
            <div className={controlClass}>
              <img style={{ paddingLeft: '15px', paddingRight: '15px' }} src={video.thumbnailUrl} alt="" />
              {video.thumbnailUrl && (
                <Dropzone
                  ref={(node: Dropzone) => {
                    thumbnailDropzoneRef = node;
                  }}
                  onDrop={this.handleDropThumbnail}
                  disableClick={true}
                  multiple={false}
                  accept="image/jpeg, image/png"
                >
                  {() => (
                    <div style={{ textAlign: 'right' }}>
                      <Button
                        bsStyle={newThumbnailImage ? 'success' : 'primary'}
                        bsSize="small"
                        onClick={() => {
                          thumbnailDropzoneRef && thumbnailDropzoneRef.open();
                        }}
                        style={{ marginTop: '10px' }}
                      >
                        {newThumbnailImage ? newThumbnailImage.name : 'Upload a new thumbnail'}
                      </Button>
                    </div>
                  )}
                </Dropzone>
              )}
            </div>
          </div>
        </div>

        <hr />
        <div className="clearfix" style={{ paddingBottom: '5px' }}>
          <div className="pull-right">
            <Button className="btn-space" bsStyle={!isSaveDisabled ? 'primary' : 'default'} disabled={isSaveDisabled || validationErrors.length > 0} onClick={this.updateVideo}>
              Update on Video Cloud
            </Button>
            <Link className="btn btn-default btn-space" to={`/${accountId}/manage-videos`}>
              Cancel
            </Link>
          </div>
        </div>
        {validationErrors.length > 0 &&
          validationErrors.map((validationError, ix) => (
            <div key={ix} className="form-group">
              <div className="alert alert-danger" role="alert">
                {validationError}
              </div>
            </div>
          ))}
        <Prompt when={!isSaveDisabled} message="There are unsaved changes. Are you sure you wish to leave?" />
        <Modal className="embed" show={showBrightcoveProcessingModal === true} onHide={this.hideBrightcoveProcessingModal}>
          <Modal.Header closeButton={true}>
            <Modal.Title>Uploaded content is processing...</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <p>The update that you just performed included text tracks and/or custom poster or thumbnail images.</p>
            <p>
              These files have been submitted to Brightcove for ingest and may take a while to process. The changes to these items may not be reflected immediately, however, will update once
              processing of these items has completed.
            </p>
          </Modal.Body>
          <Modal.Footer>
            <Button onClick={this.hideBrightcoveProcessingModal} bsStyle="primary">
              Ok
            </Button>
          </Modal.Footer>
        </Modal>
      </>
    );
  }
}

interface EditVideoProps {
  accountId: string;
  videoId?: string;
  connectorContext: ConnectorContext;
}

interface EditVideoState {
  isLoadingVideo: boolean;
  isSavingVideo: boolean;
  savingVideoStatus?: string;

  video?: ConnectorVideo;
  originalVideo?: ConnectorVideo;
  bcVideo?: BCVideo;
  customFields?: Array<BCCustomField>;
  newPosterImage?: ImageFile;
  newThumbnailImage?: ImageFile;
  showBrightcoveProcessingModal: boolean;
}
