import * as React from 'react';
import { Modal } from 'react-bootstrap';
import { HashRouter as Router, Route, Switch, Redirect, Link, matchPath } from 'react-router-dom';
import autobind from 'autobind-decorator';

import { ConnectorContext, ConnectorValidationResult, UserRole } from './services/connector';

import { Home } from './components/home';
import { AccountManagement, AccountView } from './components/account-management';
import { AddVideos } from './components/add-videos';
import { ManageVideos, EditVideo } from './components/manage-videos';
import { ManagePlaylists, EditPlaylist } from './components/manage-playlists';
import { ManageExperiences, PreviewExperience } from './components/manage-experiences';
import { LoadingProgress } from './components/common';
import { InstallationValidation } from './components/installation-validation';

import './Manage.css';

const RouteButton = ({ label, to, activeOnlyWhenExact, path }: { label: string; to: string; activeOnlyWhenExact?: boolean; path?: string }) => {
  const linkClass = 'btn btn-lg btn-block';
  return (
    <Route
      path={path || to}
      exact={activeOnlyWhenExact}
      // eslint-disable-next-line react/no-children-prop
      children={({ match }) => (
        <>
          {match && (
            <Link className={linkClass + ' btn-primary'} to={to}>
              {label}
            </Link>
          )}
          {!match && (
            <Link className={linkClass + ' btn-default'} to={to}>
              {label}
            </Link>
          )}
        </>
      )}
    />
  );
};

export class Manage extends React.Component<ManageProps, ManageState> {
  constructor(props: ManageProps, context?: any) {
    super(props, context);

    this.state = {
      isLoading: false,
      showInstallationModal: false,
      validationResult: {
        isValid: false,
        isSettingsListCreated: false,
        isProxyDefined: false,
        isAccountsListCreated: false,
        isVideosListCreated: false,
        areAccountsAvailable: false,
        accountEmptyAccountId: [],
        accountEmptyGroups: [],
        missingGroups: [],
        currentUserAvailableAccounts: [],
        currentUserMaxRole: UserRole.None
      }
    };
  }

  async UNSAFE_componentWillMount() {
    const { connectorContext } = this.props;

    this.setState({
      isLoading: true
    });

    const validationResult = await connectorContext.validateConfiguration();
    connectorContext.currentUserRole = validationResult.currentUserMaxRole;

    if (validationResult.isValid) {
      await connectorContext.initialize();
    }

    const pathMatch = matchPath<{ accountId: string }>(window.location.hash.replace('#', ''), {
      path: '/:accountId(\\d+)'
    });

    let accountId = '';
    if (pathMatch) {
      accountId = pathMatch.params.accountId;
    }

    if (!accountId && connectorContext.availableAccounts && connectorContext.availableAccounts.length > 0) {
      accountId = connectorContext.availableAccounts[0].accountId;
    }

    this.setState({
      isLoading: false,
      validationResult,
      showInstallationModal: !validationResult.isValid && (!connectorContext.currentUserRole || connectorContext.currentUserRole === UserRole.Author)
    });
  }

  @autobind
  handleAccountChange(accountId: string) {
    let accountActionPath = '';
    const pathMatch = matchPath<{ accountId: string; accountActionPath: string }>(window.location.hash.replace('#', ''), {
      path: '/:accountId(\\d+):accountActionPath(.*)?'
    });

    if (pathMatch && pathMatch.params.accountActionPath) {
      accountActionPath = pathMatch.params.accountActionPath;
    }

    window.location.hash = `/${accountId}${accountActionPath}`;
    window.location.reload();
  }

  @autobind
  hideInstallationModel() {
    this.setState({
      showInstallationModal: false
    });
  }

  render() {
    const { isLoading } = this.state;

    if (isLoading) {
      return <LoadingProgress />;
    }

    const { connectorContext } = this.props;
    const { bcApiGateway, spApiGateway } = connectorContext;

    const { validationResult, showInstallationModal } = this.state;

    const showAccountManagement =
      (connectorContext && connectorContext.currentUserRole && connectorContext.currentUserRole === UserRole.Author) ||
      validationResult.accountEmptyGroups.length > 0 ||
      validationResult.accountEmptyAccountId.length > 0 ||
      false;

    const pathMatch = matchPath<{ accountId: string }>(window.location.hash.replace('#', ''), {
      path: '/:accountId(\\d+)'
    });

    let accountId = '';
    if (pathMatch) {
      accountId = pathMatch.params.accountId;
    } else if (connectorContext.availableAccounts && connectorContext.availableAccounts.length > 0) {
      accountId = connectorContext.availableAccounts[0].accountId;
    }

    return (
      <Router>
        <div className="App">
          <div className="container body-content">
            <div className="row">
              <div className="col-md-4">
                <nav className="leftNav">
                  <RouteButton to="/" label="Home" activeOnlyWhenExact={true} path="/:accountId(\d+)?" />
                  {showAccountManagement && <RouteButton to="/account-management" label="Account Management" />}
                  {accountId && (
                    <>
                      <RouteButton to={`/${accountId}/add-videos`} label="Add Videos" path="/:accountId(\d+)/add-videos" />
                      <RouteButton to={`/${accountId}/manage-videos`} label="Manage Videos" path="/:accountId(\d+)/manage-videos" />
                      <RouteButton to={`/${accountId}/manage-playlists`} label="Manage Playlists" path="/:accountId(\d+)/manage-playlists" />
                      <RouteButton to={`/${accountId}/manage-experiences`} label="Manage Experiences" path="/:accountId(\d+)/manage-experiences" />
                    </>
                  )}
                </nav>
              </div>
              <div className="col-md-8">
                <Switch>
                  <Route path="/:accountId(\d+)?" exact={true} component={Home} />
                  <Route path="/account-management" exact={true}>
                    <AccountManagement connectorContext={connectorContext} bcApiGateway={bcApiGateway} spApiGateway={spApiGateway} validationResult={validationResult} />
                  </Route>
                  <Route
                    path="/account-management/view/:accountSharePointId"
                    render={props => <AccountView connectorContext={connectorContext} accountId={props.match.params.accountSharePointId} validationResult={validationResult} />}
                  />
                  <Route path="/:accountId/add-videos" render={() => <AddVideos connectorContext={connectorContext} accountId={accountId} onAccountChange={this.handleAccountChange} />} />
                  <Route path="/:accountId/manage-videos" exact={true}>
                    <ManageVideos accountId={accountId} connectorContext={connectorContext} onAccountChange={this.handleAccountChange} />
                  </Route>
                  <Route
                    path="/:accountId/manage-videos/:videoId/edit"
                    render={props => <EditVideo connectorContext={connectorContext} accountId={accountId} videoId={props.match.params.videoId} />}
                  />
                  <Route path="/:accountId/manage-playlists" exact={true}>
                    <ManagePlaylists accountId={accountId} connectorContext={connectorContext} bcApiGateway={bcApiGateway} spApiGateway={spApiGateway} onAccountChange={this.handleAccountChange} />
                  </Route>
                  <Route path="/:accountId/manage-playlists/new" render={() => <EditPlaylist accountId={accountId} bcApiGateway={bcApiGateway} spApiGateway={spApiGateway} />} />
                  <Route
                    path="/:accountId/manage-playlists/:playlistId/edit"
                    render={props => <EditPlaylist bcApiGateway={bcApiGateway} spApiGateway={spApiGateway} accountId={accountId} playlistId={props.match.params.playlistId} />}
                  />
                  <Route path="/:accountId/manage-experiences" exact={true}>
                    <ManageExperiences accountId={accountId} connectorContext={connectorContext} bcApiGateway={bcApiGateway} spApiGateway={spApiGateway} onAccountChange={this.handleAccountChange} />
                  </Route>
                  <Route
                    path="/:accountId/manage-experiences/:experienceId/preview"
                    render={props => (
                      <PreviewExperience
                        connectorContext={connectorContext}
                        bcApiGateway={bcApiGateway}
                        spApiGateway={spApiGateway}
                        accountId={accountId}
                        experienceId={props.match.params.experienceId}
                      />
                    )}
                  />
                  {accountId && <Redirect to={`/${accountId}`} />}
                  {!accountId && <Redirect to={`/`} />}
                </Switch>
              </div>
            </div>
            <hr />
            <footer>
              <p className="copyright">© 2018 - Brightcove</p>
              <p className="version">{process.env.REACT_APP_VERSION}</p>
            </footer>
          </div>
          <Modal className="installation-modal" show={showInstallationModal} onHide={this.hideInstallationModel} bsSize="large">
            <Modal.Header closeButton={true}>
              <Modal.Title>Brightcove Video Connector for SharePoint Installation</Modal.Title>
            </Modal.Header>
            <Modal.Body>
              <InstallationValidation spApiGateway={connectorContext.spApiGateway} validationResult={validationResult} />
            </Modal.Body>
            <Modal.Footer />
          </Modal>
        </div>
      </Router>
    );
  }
}

interface ManageProps {
  connectorContext: ConnectorContext;
  raiseError: (message: string, err?: Error) => boolean;
}

interface ManageState {
  isLoading: boolean;
  validationResult: ConnectorValidationResult;
  showInstallationModal: boolean;
}

export default Manage;
