import { VideoSearchParameters, ConnectorVideo, mapBCVideoToMVideo, ConnectorContext } from '..';
import { filter } from 'lodash';

/**
 * See https://support.brightcove.com/cmsplayback-api-videos-search#combinesearchcriteria
 * @param this
 * @param searchParams
 */
export async function searchForBrightcoveVideos(this: ConnectorContext, accountId: string, searchParams: Partial<VideoSearchParameters>): Promise<Array<ConnectorVideo>> {
  if (!accountId) {
    return [];
  }

  const { bcApiGateway } = this;
  const videosApi = bcApiGateway.videosApi;
  const bcParams = this.getBCVideoSearchParams(searchParams);
  let videos = await videosApi.getVideos(accountId, bcParams);

  if (searchParams.folderId) {
    // eslint-disable-next-line @typescript-eslint/camelcase
    videos = filter(videos, { folder_id: searchParams.folderId });
  }

  const result: Array<ConnectorVideo> = [];
  for (const video of videos) {
    result.push(mapBCVideoToMVideo(video));
  }
  return result;
}
