import { observable, action, computed } from 'mobx';
import { BrightcoveAccount } from 'services';
import { SharePointApiGateway, SPAddinContext, SPGroup } from '../sharepoint';
import { BCSPAccessTokenService } from '../BCSPAccessTokenService';
import { BrightcoveApiGateway, BCGetVideosParams, BCGetPlaylistsParams, BCPlayer } from '@brightcove/video-cloud-sdk';
import { UserRole, ConnectorValidationResult, ConnectorVideoUpload, ConnectorVideo, VideoSearchParameters, SortOrder, VideoSortField, PlaylistSearchParameters, PlaylistSortField } from '.';

import { validateConfiguration } from './behaviors/validateConfiguration';
import { performDynamicIngest } from './behaviors/performDynamicIngest';
import { searchForBrightcoveVideos } from './behaviors/searchForBrightcoveVideos';

import { setup } from 'sp-pnp-js';
import { defaultsDeep, find } from 'lodash';
import { bind } from 'lodash-decorators';
import URI from 'urijs';

export class ConnectorContext {
  @observable
  public allAccounts: Array<BrightcoveAccount>;

  public addinWebId?: string;
  public currentUserRole?: UserRole;

  @observable
  public userGroups: Array<SPGroup>;

  @observable
  public doesEmbedHelperExist: boolean | undefined;

  private readonly onError: (message: string, err?: Error) => boolean | undefined;
  private readonly _spAddinContext: SPAddinContext;
  private readonly _bcApiGateway: BrightcoveApiGateway;
  private readonly _spApiGateway: SharePointApiGateway;
  private readonly _players: { [accountId: string]: BCPlayer[] } = {};

  constructor(onError: (message: string, err?: Error) => boolean) {
    this.onError = onError;

    // Set up pnp
    setup({
      sp: {
        headers: {
          Accept: 'application/json; odata=verbose'
        }
      }
    });

    // Get information passed to the SharePoint addin via the query string parameters.
    const uri = URI();
    this._spAddinContext = uri.query(true) as SPAddinContext;

    // If we're in development mode, default what we normally would have picked off the querystring to environment variables
    if (process.env.NODE_ENV !== 'production' || process.env.REACT_APP_UseEnv === 'true') {
      defaultsDeep(this._spAddinContext, {
        ProductId: process.env.REACT_APP_ProductId,
        SPHostUrl: process.env.REACT_APP_SPHostUrl,
        SPAppWebUrl: process.env.REACT_APP_SPAppWebUrl || window.location.origin,
        SPLanguage: process.env.REACT_APP_SPLanguage,
        SPClientTag: process.env.REACT_APP_SPClientTag,
        SPProductNumber: process.env.REACT_APP_SPProductNumber,
        Referrer: process.env.REACT_APP_Referrer || document.referrer
      });
    }

    if (this._spAddinContext.SPHostUrl === 'default') {
      this.spAddinContext.SPHostUrl = '';
    }

    if (this._spAddinContext.SPAppWebUrl === 'default') {
      this.spAddinContext.SPAppWebUrl = '';
    }

    if (!this._spAddinContext.Referrer || this._spAddinContext.Referrer === 'default') {
      this.spAddinContext.Referrer = document.referrer;
    }

    this._spApiGateway = new SharePointApiGateway(this._spAddinContext, this.onError);

    const accessTokenService = new BCSPAccessTokenService(this._spApiGateway, this.onError);
    this._bcApiGateway = new BrightcoveApiGateway(accessTokenService, this.onError);

    this.allAccounts = [];
    this.userGroups = [];
  }

  /**
   * Gets a collection of accounts that the current user has access to.
   */
  @computed
  get availableAccounts(): Array<BrightcoveAccount> {
    const availableAccounts: Array<BrightcoveAccount> = [];
    if (!this.allAccounts) {
      return availableAccounts;
    }

    for (const account of this.allAccounts) {
      if (!account.accountAuthorsGroupName || !account.accountViewersGroupName) {
        continue;
      }

      let currentUserIsAuthor = false,
        currentUserIsViewer = false;

      if (find(this.userGroups, { title: account.accountAuthorsGroupName })) {
        currentUserIsAuthor = true;
      }

      if (find(this.userGroups, { title: account.accountViewersGroupName })) {
        currentUserIsViewer = true;
      }

      if (currentUserIsAuthor || currentUserIsViewer) {
        availableAccounts.push(account);
      }
    }

    return availableAccounts;
  }

  get bcApiGateway(): BrightcoveApiGateway {
    return this._bcApiGateway;
  }

  get spAddinContext(): SPAddinContext {
    return this._spAddinContext;
  }

  get spApiGateway(): SharePointApiGateway {
    return this._spApiGateway;
  }

  getBCPlaylistSearchParams(searchParams: Partial<PlaylistSearchParameters>): BCGetPlaylistsParams {
    // Map search params to 'q'
    const bcParams: BCGetPlaylistsParams = {
      limit: searchParams.searchLimit || 45,
      sort: searchParams.sortOrder === SortOrder.Descending ? '-' : '',
      q: searchParams.searchTerms ? `${searchParams.searchTerms}` : ''
    };

    switch (searchParams.sortField) {
      case PlaylistSortField.DisplayName:
        bcParams.sort += 'name';
        break;
      case PlaylistSortField.ModifiedDate:
        bcParams.sort += 'updated_at';
        break;
      default:
        // Do nothing.
        break;
    }

    return bcParams;
  }

  getBCVideoSearchParams(searchParams: Partial<VideoSearchParameters>): BCGetVideosParams {
    // Map search params to 'q'
    const bcParams: BCGetVideosParams = {
      limit: searchParams.searchLimit || 45,
      sort: searchParams.sortOrder === SortOrder.Descending ? '-' : '',
      q: searchParams.searchTerms ? `${searchParams.searchTerms}` : ''
    };

    // Remove videos that are tagged with the addin web id.
    if (this.addinWebId) {
      bcParams.q += `-tags:"bcsp-${this.addinWebId.replace(/-/g, '')}-1"`;
    }

    switch (searchParams.sortField) {
      case VideoSortField.Name:
        bcParams.sort += 'name';
        break;
      case VideoSortField.TotalPlays:
        bcParams.sort += 'plays_total';
        break;
      case VideoSortField.UpdatedDate:
        bcParams.sort += 'updated_at';
        break;
      case VideoSortField.CreationDate:
        bcParams.sort += 'created_at';
        break;
      case VideoSortField.StartDate:
        bcParams.sort += 'schedule_starts_at';
        break;
      default:
        throw new Error('Unknown or unsupported sort field: ' + searchParams.sortField);
    }

    if (searchParams.folderId) {
      bcParams.limit = 1000;
      // bcParams.q = 'folder_id:' + searchParams.folderId;
    }

    return bcParams;
  }

  @action
  public async initialize() {
    await this.refreshAccounts();
    await this.refreshUserGroups();
    await this.refreshEmbedHelper();
  }

  public raiseError(message: string, err?: Error): boolean | undefined {
    return this.onError(message, err);
  }

  @action
  public async refreshAccounts() {
    const settings = await this.spApiGateway.settingsManager.loadBrightcoveSettings();
    this.allAccounts = settings.brightcoveAccounts;
  }

  @action
  public async refreshEmbedHelper() {
    this.doesEmbedHelperExist = await this.spApiGateway.spEmbedUtil.doesEmbedHelperExist();
  }

  @action
  public async refreshUserGroups(currentUserEmail?: string) {
    if (!currentUserEmail && process.env.REACT_APP_BC_Override_Current_User !== 'default') {
      currentUserEmail = process.env.REACT_APP_BC_Override_Current_User;
    }

    this.userGroups = await this.spApiGateway.spGroups.getUserGroups(currentUserEmail);
  }

  @bind
  @action
  public async retrieveAllPlayers(accountId: string, force?: boolean): Promise<BCPlayer[]> {
    if ((force === undefined || force === false) && this._players[accountId] !== undefined) {
      return this._players[accountId];
    }

    const { playerManagementApi } = this.bcApiGateway;

    try {
      const playersResponse = await playerManagementApi.getAllPlayers(accountId);
      if (playersResponse && playersResponse.item_count > 0 && playersResponse.items) {
        this._players[accountId] = playersResponse.items;
        return this._players[accountId];
      }
    } catch (ex) {
      // Do Nothing.
    }

    return [];
  }

  public async searchForBrightcoveVideos(accountId: string, searchParams: Partial<VideoSearchParameters>) {
    return searchForBrightcoveVideos.call(this, accountId, searchParams) as Promise<Array<ConnectorVideo>>;
  }

  public async performDynamicIngest(video: ConnectorVideoUpload, statusUpdateCallback: (video: ConnectorVideoUpload) => void): Promise<void> {
    return performDynamicIngest.call(this, video, statusUpdateCallback) as Promise<void>;
  }

  public async validateConfiguration(currentUserEmail?: string): Promise<ConnectorValidationResult> {
    if (!currentUserEmail && process.env.REACT_APP_BC_Override_Current_User !== 'default') {
      currentUserEmail = process.env.REACT_APP_BC_Override_Current_User;
    }
    const validationResult = (await validateConfiguration.call(this, currentUserEmail)) as ConnectorValidationResult;

    return validationResult;
  }
}
