import * as React from 'react';
import { Link } from 'react-router-dom';
import { Tabs, Tab } from 'react-bootstrap';
import { BootstrapTable, TableHeaderColumn, SelectRow } from 'react-bootstrap-table';
import { Spinner } from '../common';
import { VideoSearchControls } from '../manage-videos';
import { PlaylistSearchControls } from '../manage-playlists';
import autobind from 'autobind-decorator';

import { BrightcoveExperienceProps, BrightcoveExperienceUnits, BrightcoveExperienceOverrideMode } from '.';
import { ConnectorContext, VideoSearchParameters, PlaylistSearchParameters, getDefaultPlaylistSearchParameters, getDefaultVideoSearchParameters } from '../../services/connector';
import { BCVideo, BCPlaylist } from '@brightcove/video-cloud-sdk';

import { without, map, filter } from 'lodash';

export class BrightcoveExperienceControls extends React.Component<BrightcoveExperienceControlsProps, BrightcoveExperienceControlsState> {
  constructor(props: BrightcoveExperienceControlsProps, context?: any) {
    super(props, context);

    this.state = {
      isLoading: false,
      playlistSearchParameters: getDefaultPlaylistSearchParameters(),
      playlists: [],
      videoSearchParameters: getDefaultVideoSearchParameters(),
      videos: []
    };
  }

  async componentDidMount() {
    const { bcExperienceProps } = this.props;

    if (bcExperienceProps.overrideExperienceVideos) {
      this.loadOverrideItems(bcExperienceProps.experienceOverrideMode || BrightcoveExperienceOverrideMode.Videos);
    }
  }

  @autobind
  async searchForVideos(accountId: string, searchParams: Partial<VideoSearchParameters>): Promise<void> {
    const { connectorContext } = this.props;
    const { videosApi } = connectorContext.bcApiGateway;

    if (!accountId) {
      return;
    }

    this.setState({
      isLoading: true
    });

    const bcSearchParams = await connectorContext.getBCVideoSearchParams(searchParams);
    let videos = await videosApi.getVideos(accountId, bcSearchParams);

    if (searchParams.folderId) {
      // eslint-disable-next-line @typescript-eslint/camelcase
      videos = filter(videos, { folder_id: searchParams.folderId });
    }

    this.setState({
      isLoading: false,
      videos
    });
  }

  @autobind
  async searchForPlaylists(accountId: string, searchParams: Partial<PlaylistSearchParameters>): Promise<void> {
    const { connectorContext } = this.props;
    const { playlistsApi } = connectorContext.bcApiGateway;

    if (!accountId) {
      return;
    }

    this.setState({
      isLoading: true
    });

    const bcSearchParams = await connectorContext.getBCPlaylistSearchParams(searchParams);
    const playlists = await playlistsApi.getPlaylists(accountId, bcSearchParams);

    this.setState({
      isLoading: false,
      playlists
    });
  }

  @autobind
  handleEmbedTypeChange(event: React.ChangeEvent<HTMLInputElement>) {
    const { bcExperienceProps, onExperiencePropsChanged } = this.props;
    bcExperienceProps.embedType = event.target.value as 'Standard' | 'Advanced';
    if (bcExperienceProps.embedType === 'Standard') {
      bcExperienceProps.sizing = 'Fixed';
    }

    if (bcExperienceProps.embedType === 'Advanced') {
      bcExperienceProps.overrideExperienceVideos = false;
    }

    onExperiencePropsChanged(bcExperienceProps);
  }

  @autobind
  handleSizingChange(event: React.ChangeEvent<HTMLInputElement>) {
    const { bcExperienceProps, onExperiencePropsChanged } = this.props;
    bcExperienceProps.sizing = event.target.value as 'Responsive' | 'Fixed';
    onExperiencePropsChanged(bcExperienceProps);
  }

  @autobind
  handleOverrideWidthChange(event: React.ChangeEvent<HTMLInputElement>) {
    const { bcExperienceProps, onExperiencePropsChanged } = this.props;
    bcExperienceProps.overrideWidth = event.target.checked;
    onExperiencePropsChanged(bcExperienceProps);
  }

  @autobind
  handlePlayerWidthChange(event: React.ChangeEvent<HTMLInputElement>) {
    const { bcExperienceProps, onExperiencePropsChanged } = this.props;
    bcExperienceProps.playerWidth = event.target.value;
    onExperiencePropsChanged(bcExperienceProps);
  }

  @autobind
  handleOverrideHeightChange(event: React.ChangeEvent<HTMLInputElement>) {
    const { bcExperienceProps, onExperiencePropsChanged } = this.props;
    bcExperienceProps.overrideHeight = event.target.checked;
    onExperiencePropsChanged(bcExperienceProps);
  }

  @autobind
  handlePlayerHeightChange(event: React.ChangeEvent<HTMLInputElement>) {
    const { bcExperienceProps, onExperiencePropsChanged } = this.props;
    bcExperienceProps.playerHeight = event.target.value;
    onExperiencePropsChanged(bcExperienceProps);
  }

  @autobind
  handlePlayerUnitsChange(event: React.ChangeEvent<HTMLSelectElement>) {
    const { bcExperienceProps, onExperiencePropsChanged } = this.props;
    bcExperienceProps.playerUnits = event.target.value as BrightcoveExperienceUnits;

    onExperiencePropsChanged(bcExperienceProps);
  }

  @autobind
  handleOverrideExperienceVideosChange(event: React.ChangeEvent<HTMLInputElement>) {
    const { bcExperienceProps, onExperiencePropsChanged } = this.props;
    bcExperienceProps.overrideExperienceVideos = event.target.checked;
    bcExperienceProps.experienceOverrideMode = bcExperienceProps.experienceOverrideMode || BrightcoveExperienceOverrideMode.Videos;

    if (event.target.checked) {
      this.handleExperienceOverrideModeChange(bcExperienceProps.experienceOverrideMode);
    }

    onExperiencePropsChanged(bcExperienceProps);
  }

  @autobind
  async loadOverrideItems(selectedMode: BrightcoveExperienceOverrideMode) {
    switch (selectedMode) {
      case BrightcoveExperienceOverrideMode.Playlist:
        const { playlistSearchParameters } = this.state;
        this.searchForPlaylists(this.props.accountId, playlistSearchParameters);
        break;
      case BrightcoveExperienceOverrideMode.Videos:
        const { videoSearchParameters } = this.state;
        this.searchForVideos(this.props.accountId, videoSearchParameters);
        break;
      default:
        throw new Error('Unknown or unsupported experience override mode.');
    }
  }

  @autobind
  handleExperienceOverrideModeChange(eventKey: any) {
    const { bcExperienceProps, onExperiencePropsChanged } = this.props;
    const selectedMode = eventKey as BrightcoveExperienceOverrideMode;

    this.loadOverrideItems(selectedMode);

    bcExperienceProps.experienceOverrideMode = selectedMode;
    bcExperienceProps.experienceOverrideModeIds = [];
    onExperiencePropsChanged(bcExperienceProps);
  }

  @autobind
  handleVideoSearchParametersChanged(videoSearchParameters: Partial<VideoSearchParameters>, fieldChanged?: string) {
    this.setState({
      videoSearchParameters
    });

    if (fieldChanged !== 'searchTerms') {
      this.searchForVideos(this.props.accountId, videoSearchParameters);
    }
  }

  @autobind
  handlePlaylistSearchParametersChanged(playlistSearchParameters: Partial<PlaylistSearchParameters>, fieldChanged?: string) {
    this.setState({
      playlistSearchParameters
    });

    if (fieldChanged !== 'searchTerms') {
      this.searchForPlaylists(this.props.accountId, playlistSearchParameters);
    }
  }

  @autobind
  handlePlaylistSelected(playlist: BCPlaylist, isSelected: boolean) {
    const { bcExperienceProps, onExperiencePropsChanged } = this.props;
    if (isSelected) {
      bcExperienceProps.experienceOverrideModeIds = [playlist.id];
    } else {
      bcExperienceProps.experienceOverrideModeIds = [];
    }

    onExperiencePropsChanged(bcExperienceProps);
  }

  @autobind
  handleAllVideosSelected(isSelected: boolean) {
    const { bcExperienceProps, onExperiencePropsChanged } = this.props;
    const { videos } = this.state;
    if (isSelected) {
      bcExperienceProps.experienceOverrideModeIds = map(videos, 'id');
    } else {
      bcExperienceProps.experienceOverrideModeIds = [];
    }

    onExperiencePropsChanged(bcExperienceProps);
    return true;
  }

  @autobind
  handleVideoSelected(video: BCVideo, isSelected: boolean) {
    const { bcExperienceProps, onExperiencePropsChanged } = this.props;
    bcExperienceProps.experienceOverrideModeIds = without(bcExperienceProps.experienceOverrideModeIds, video.id);
    if (isSelected) {
      bcExperienceProps.experienceOverrideModeIds.push(video.id);
    }

    onExperiencePropsChanged(bcExperienceProps);
  }

  @autobind
  renderPlaylistNameCell(cell: any, playlist: BCPlaylist) {
    const { bcExperienceProps } = this.props;

    return (
      <div className="row">
        <div className="col-xs-12">
          <Link className="small" to={`/${bcExperienceProps.accountId}/manage-playlists/${playlist.id}/edit`} target="_blank">
            {playlist.name}
          </Link>
          <div className="small">{playlist.id}</div>
        </div>
      </div>
    );
  }

  @autobind
  renderVideoNameCell(cell: any, video: BCVideo) {
    const { bcExperienceProps } = this.props;

    return (
      <div className="row">
        {video.images && video.images.thumbnail && <img className="col-xs-2" src={video.images.thumbnail.src} alt="" />}
        <div className="col-xs-10">
          <Link className="small" to={`/${bcExperienceProps.accountId}/manage-videos/${video.id}/edit`} target="_blank">
            {video.name}
          </Link>
          <div className="small">{video.id}</div>
        </div>
      </div>
    );
  }

  render() {
    const {
      connectorContext,
      accountId,
      labelClass,
      controlClass,
      formControlClass,
      bcExperienceProps,
      showEmbedCustomizations,
      showAdvancedEmbedCustomizations,
      disableOverrideForJavaScriptEmbeds
    } = this.props;
    const { isLoading, videoSearchParameters, playlistSearchParameters, videos, playlists } = this.state;

    const selectPlaylistRow: SelectRow = {
      mode: 'radio',
      clickToSelect: true,
      className: 'info',
      onSelect: this.handlePlaylistSelected,
      selected: bcExperienceProps.experienceOverrideModeIds
    };

    const selectVideoRow: SelectRow = {
      mode: 'checkbox',
      clickToSelect: true,
      className: 'info',
      onSelect: this.handleVideoSelected,
      onSelectAll: this.handleAllVideosSelected,
      selected: bcExperienceProps.experienceOverrideModeIds
    };

    return (
      <>
        {showEmbedCustomizations === true && (
          <>
            <div className="form-group">
              <label className={labelClass}>Embed Type</label>
              <div className={controlClass}>
                <div className={`input-group ${formControlClass}`}>
                  <label className="radio-inline">
                    <input type="radio" name="embedType" value="Standard" checked={bcExperienceProps.embedType === 'Standard'} onChange={this.handleEmbedTypeChange} /> iFrame
                  </label>
                  <label className="radio-inline">
                    <input type="radio" name="embedType" value="Advanced" checked={bcExperienceProps.embedType === 'Advanced'} onChange={this.handleEmbedTypeChange} /> JavaScript
                  </label>
                </div>
              </div>
            </div>
            {showAdvancedEmbedCustomizations === true && (
              <>
                <div className="form-group">
                  <label className={labelClass}>Sizing</label>
                  <div className={controlClass}>
                    <div className={`input-group ${formControlClass}`}>
                      <label className="radio-inline">
                        <input type="radio" name="sizing" value="Fixed" checked={bcExperienceProps.sizing === 'Fixed'} onChange={this.handleSizingChange} /> Fixed
                      </label>
                      <label className="radio-inline">
                        <input
                          type="radio"
                          name="sizing"
                          value="Responsive"
                          disabled={bcExperienceProps.embedType === 'Standard'}
                          checked={bcExperienceProps.sizing === 'Responsive'}
                          onChange={this.handleSizingChange}
                        />{' '}
                        Responsive
                      </label>
                    </div>
                  </div>
                </div>
                <div className="form-group">
                  <label className={labelClass}>Layout Width</label>
                  <div className={controlClass}>
                    <div className="form-row">
                      <div className="col-xs-4">
                        <div className="input-group">
                          <input
                            className={`form-control ${formControlClass}`}
                            type="number"
                            placeholder="auto"
                            value={bcExperienceProps.playerWidth}
                            disabled={!bcExperienceProps.overrideWidth || (bcExperienceProps.embedType === 'Advanced' && bcExperienceProps.sizing === 'Responsive')}
                            onChange={this.handlePlayerWidthChange}
                          />
                          <span className="input-group-addon">px</span>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="form-group">
                  <label className={labelClass}>Layout Height</label>
                  <div className={controlClass}>
                    <div className="form-row">
                      <div className="col-xs-4">
                        <div className="input-group">
                          <input
                            className={`form-control ${formControlClass}`}
                            type="number"
                            placeholder="auto"
                            value={bcExperienceProps.playerHeight}
                            disabled={!bcExperienceProps.overrideHeight || (bcExperienceProps.embedType === 'Advanced' && bcExperienceProps.sizing === 'Responsive')}
                            onChange={this.handlePlayerHeightChange}
                          />
                          <span className="input-group-addon">px</span>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </>
            )}
          </>
        )}
        {((disableOverrideForJavaScriptEmbeds && bcExperienceProps.embedType === 'Standard') || !disableOverrideForJavaScriptEmbeds) && (
          <div className="form-group">
            <label className={labelClass}>Override Experience Videos</label>
            <div className={controlClass}>
              <input className="form-check-input" type="checkbox" checked={bcExperienceProps.overrideExperienceVideos} onChange={this.handleOverrideExperienceVideosChange} />
              &nbsp;Check here if you would like to override the experience-defined videos with a custom video or playlist selection.
            </div>
          </div>
        )}
        {bcExperienceProps.overrideExperienceVideos && (
          <Tabs id="OverrideExperienceVideosTabs" activeKey={bcExperienceProps.experienceOverrideMode} onSelect={this.handleExperienceOverrideModeChange} style={{ paddingBottom: '5px' }}>
            <Tab eventKey={BrightcoveExperienceOverrideMode.Videos} title="Videos">
              <VideoSearchControls
                accountId={accountId}
                connectorContext={connectorContext}
                showSearchTarget={false}
                labelClass={labelClass}
                controlClass={controlClass}
                searchParameters={videoSearchParameters}
                onSearchParametersChanged={this.handleVideoSearchParametersChanged}
                onSearchForVideos={this.searchForVideos}
              />
              {isLoading && accountId && <Spinner loadingText="Loading Videos..." />}
              {!isLoading && accountId && (
                <div className="well">
                  <BootstrapTable data={videos} striped={true} bordered={false} selectRow={selectVideoRow} height="250">
                    <TableHeaderColumn dataField="name" dataFormat={this.renderVideoNameCell} formatExtraData={{ accountId }}>
                      Videos
                    </TableHeaderColumn>
                    <TableHeaderColumn dataField="id" isKey={true} hidden={true}>
                      ID
                    </TableHeaderColumn>
                  </BootstrapTable>
                  <hr />
                </div>
              )}
            </Tab>
            <Tab eventKey={BrightcoveExperienceOverrideMode.Playlist} title="Playlist">
              <PlaylistSearchControls
                accountId={accountId}
                labelClass={labelClass}
                controlClass={controlClass}
                searchParameters={playlistSearchParameters}
                onSearchParametersChanged={this.handlePlaylistSearchParametersChanged}
                onSearchForPlaylists={this.searchForPlaylists}
              />
              {isLoading && accountId && <Spinner loadingText="Loading Playlists..." />}
              {!isLoading && accountId && (
                <div className="well">
                  <BootstrapTable data={playlists} striped={true} bordered={false} selectRow={selectPlaylistRow} height="250">
                    <TableHeaderColumn dataField="name" dataFormat={this.renderPlaylistNameCell} formatExtraData={{ accountId }}>
                      Playlist
                    </TableHeaderColumn>
                    <TableHeaderColumn dataField="id" isKey={true} hidden={true}>
                      ID
                    </TableHeaderColumn>
                  </BootstrapTable>
                  <hr />
                </div>
              )}
            </Tab>
          </Tabs>
        )}
      </>
    );
  }
}

export interface BrightcoveExperienceControlsProps {
  accountId: string;
  connectorContext: ConnectorContext;
  labelClass?: string;
  controlClass?: string;
  formControlClass?: string;
  showEmbedCustomizations: boolean;
  showAdvancedEmbedCustomizations: boolean;
  disableOverrideForJavaScriptEmbeds: boolean;
  bcExperienceProps: Partial<BrightcoveExperienceProps>;
  onExperiencePropsChanged: (bcExperienceProps: Partial<BrightcoveExperienceProps>) => void;
}

export interface BrightcoveExperienceControlsState {
  isLoading: boolean;
  playlistSearchParameters: Partial<PlaylistSearchParameters>;
  playlists: Array<BCPlaylist>;
  videoSearchParameters: Partial<VideoSearchParameters>;
  videos: Array<BCVideo>;
}
