import * as React from 'react';
import autobind from 'autobind-decorator';

import { BrightcoveExperienceUnits, BrightcoveExperienceOverrideMode } from '.';
import { BCExperience } from '@brightcove/video-cloud-sdk';

import URI from 'urijs';

export class BrightcoveExperience extends React.Component<BrightcoveExperienceProps> {
  private _scriptElement: HTMLScriptElement | null | undefined;
  private _embedElement: HTMLDivElement | null | undefined;

  componentDidMount() {
    const { embedCodeOverride } = this.props;
    if (this._embedElement && embedCodeOverride) {
      const range = document.createRange();
      range.selectNodeContents(this._embedElement);
      range.deleteContents();
      this._embedElement.appendChild(range.createContextualFragment(embedCodeOverride));
    }

    if (this._scriptElement && this._scriptElement.parentElement) {
      const parentElement = this._scriptElement.parentElement;

      const newScriptElement = document.createElement('script');
      newScriptElement.src = this._scriptElement.src;
      parentElement.removeChild(this._scriptElement);
      parentElement.appendChild(newScriptElement);
    }
  }

  @autobind
  renderIFrameFixed() {
    const { accountId, experienceId, renderMode, overrideExperienceVideos, experienceOverrideMode, experienceOverrideModeIds } = this.props;

    const scriptUri = new URI(`https://players.brightcove.net/${accountId}/experience_${experienceId}/index.html`);
    scriptUri.addQuery('usage', `cms:sharepoint:${this.props.sharepointVersion}:${this.props.connectorVersion}:experienceiframe`);
    if (overrideExperienceVideos) {
      switch (experienceOverrideMode) {
        case BrightcoveExperienceOverrideMode.Playlist:
          if (experienceOverrideModeIds) {
            scriptUri.addQuery('videoIds', '');
            scriptUri.addQuery('playlistId', experienceOverrideModeIds.join(','));
          }
          break;
        default:
          if (experienceOverrideModeIds) {
            scriptUri.addQuery('videoIds', experienceOverrideModeIds.join(','));
            scriptUri.addQuery('playlistId', '');
          }
          break;
      }
    }

    const scriptUrl = scriptUri.toString().replace(/%2C/g, ',');

    if (renderMode === 'Embed') {
      return (
        <div style={{ display: 'block', position: 'relative', width: '640px', height: '640px' }}>
          <iframe
            title="Embed"
            style={{ width: '100%', height: '100%', top: '0px', bottom: '0px', right: '0px', left: '0px', border: 'none', marginLeft: 'auto', marginRight: 'auto', position: 'absolute' }}
            src={scriptUrl}
            allowFullScreen={true}
            webkitallowfullscreen=""
            mozallowfullscreen=""
          />
        </div>
      );
    }

    return (
      <iframe
        title="Full"
        style={{ width: '100%', height: '100%', top: '0px', bottom: '0px', right: '0px', left: '0px', border: 'none', marginLeft: 'auto', marginRight: 'auto', position: 'absolute' }}
        src={scriptUrl}
        allowFullScreen={true}
        webkitallowfullscreen=""
        mozallowfullscreen=""
      />
    );
  }

  @autobind
  renderJavaScriptFixed() {
    const { accountId, experienceId, overrideExperienceVideos, experienceOverrideMode, experienceOverrideModeIds } = this.props;

    const scriptUrl = `https://players.brightcove.net/${accountId}/experience_${experienceId}/live.js`;

    const divProps: any = {
      'data-experience': experienceId,
      'data-usage': `cms:sharepoint:${this.props.sharepointVersion}:${this.props.connectorVersion}:experiencejavascript`
    };

    if (overrideExperienceVideos) {
      switch (experienceOverrideMode) {
        case BrightcoveExperienceOverrideMode.Playlist:
          if (experienceOverrideModeIds) {
            divProps['data-video-ids'] = '';
            divProps['data-playlist-id'] = experienceOverrideModeIds.join(',');
          }
          break;
        default:
        case BrightcoveExperienceOverrideMode.Videos:
          if (experienceOverrideModeIds) {
            divProps['data-video-ids'] = experienceOverrideModeIds.join(',');
            divProps['data-playlist-id'] = '';
          }
          break;
      }
    }

    return (
      <>
        <div {...divProps} />
        <script ref={el => (this._scriptElement = el)} src={scriptUrl} />
      </>
    );
  }

  @autobind
  renderJavaScriptResponsive() {
    const { accountId, experienceId, overrideExperienceVideos, experienceOverrideMode, experienceOverrideModeIds } = this.props;

    const scriptUrl = `https://players.brightcove.net/${accountId}/experience_${experienceId}/live.js`;

    const divProps: any = {
      'data-experience': experienceId,
      'data-usage': `cms:sharepoint:${this.props.sharepointVersion}:${this.props.connectorVersion}:experiencejavascript`
    };

    if (overrideExperienceVideos) {
      switch (experienceOverrideMode) {
        case BrightcoveExperienceOverrideMode.Playlist:
          if (experienceOverrideModeIds) {
            divProps['data-video-ids'] = '';
            divProps['data-playlist-id'] = experienceOverrideModeIds.join(',');
          }
          break;
        default:
        case BrightcoveExperienceOverrideMode.Videos:
          if (experienceOverrideModeIds) {
            divProps['data-video-ids'] = experienceOverrideModeIds.join(',');
            divProps['data-playlist-id'] = '';
          }
          break;
      }
    }

    return (
      <>
        <div {...divProps} />
        <script ref={el => (this._scriptElement = el)} src={scriptUrl} />
      </>
    );
  }

  render() {
    const { embedType, sizing, embedCodeOverride } = this.props;

    if (embedCodeOverride && embedCodeOverride.length > 0) {
      const markup = { __html: embedCodeOverride };
      return <div ref={el => (this._embedElement = el)} dangerouslySetInnerHTML={markup} />;
    }

    switch (embedType) {
      case 'Standard':
        return this.renderIFrameFixed();
      case 'Advanced':
        switch (sizing) {
          case 'Fixed':
            return this.renderJavaScriptFixed();
          case 'Responsive':
            return this.renderJavaScriptResponsive();
          default:
            throw new Error('Unknown or unsupported embed sizing.');
        }
      default:
        throw new Error('Unknown or supported embed type.');
    }
  }
}

export interface BrightcoveExperienceProps {
  accountId: string;
  sharepointVersion: string;
  connectorVersion: string;
  experience?: BCExperience;
  experienceId: string;
  embedType: 'Standard' | 'Advanced';
  sizing: 'Responsive' | 'Fixed';
  overrideWidth: boolean;
  playerWidth?: string;
  overrideHeight: boolean;
  playerHeight?: string;
  playerUnits?: BrightcoveExperienceUnits;
  overrideExperienceVideos: boolean;
  experienceOverrideMode: BrightcoveExperienceOverrideMode;
  experienceOverrideModeIds?: Array<string>;
  embedCodeOverride?: string;
  renderMode?: 'Full' | 'Part' | 'Embed';
}
