import { BrightcovePlayerProps, BrightcovePlayerAspectRatio } from '.';
import URI from 'urijs';

export class BrightcovePlayerUtils {
  public static getDefaultBrightcovePlayerProps(): Partial<BrightcovePlayerProps> {
    return {
      connectorVersion: process.env.REACT_APP_VERSION,
      autoplay: false,
      muted: false,
      embedType: 'Advanced',
      sizing: 'Responsive',
      playerNumThumbnails: 5,
      aspectRatio: BrightcovePlayerAspectRatio.Widescreen,
      playerWidth: '640',
      playerHeight: '360',
      units: 'px'
    };
  }

  public static getDefaultBrightcovePlaylistProps(): Partial<BrightcovePlayerProps> {
    return {
      mode: 'Playlist',
      connectorVersion: process.env.REACT_APP_VERSION,
      autoplay: false,
      muted: false,
      embedType: 'Standard',
      sizing: 'Fixed',
      playerNumThumbnails: 5,
      aspectRatio: BrightcovePlayerAspectRatio.Widescreen,
      playerWidth: '640',
      playerHeight: '360',
      units: 'px'
    };
  }

  public static getBrightcovePlayerPropsFromQueryString(): Partial<BrightcovePlayerProps> {
    const uri = URI();
    const info = uri.query(true) as { [key: string]: string };

    const result = BrightcovePlayerUtils.getDefaultBrightcovePlayerProps();

    result.accountId = info.accountId;
    switch (info.part) {
      case 'playlist':
        result.mode = 'Playlist';
        break;
      case 'video':
      default:
        result.mode = 'Video';
        break;
    }

    const body = document.body;
    const html = document.documentElement;

    result.playerWidth = `${Math.max(body.scrollWidth, body.offsetWidth, html.clientWidth, html.scrollWidth, html.offsetWidth)}`;
    // result.playerHeight = `${Math.max(body.scrollHeight, body.offsetHeight, html.clientHeight, html.scrollHeight, html.offsetHeight)}`;

    result.playerId = info.playerId || 'default';
    result.modeId = info.modeId;
    result.sharepointVersion = info.SPProductNumber;

    if (info.autoplay === 'true') {
      result.autoplay = true;
    }

    if (info.muted === 'true') {
      result.muted = true;
    }

    if (info.embedType === 'Standard' || info.embedType === 'Advanced') {
      result.embedType = info.embedType;
    } else {
      result.embedType = 'Advanced';
    }

    if (info.sizing === 'Fixed' || info.sizing === 'Responsive') {
      result.sizing = info.sizing;
    } else {
      result.sizing = 'Responsive';
    }

    return result;
  }

  public static augmentCurrentUriWithPlayerProps(bcPlayerProps: Partial<BrightcovePlayerProps>) {
    const uri = URI();

    uri.setQuery('accountId', bcPlayerProps.accountId || '');
    uri.setQuery('playerId', bcPlayerProps.playerId || 'default');
    uri.setQuery('modeId', bcPlayerProps.modeId || '');
    uri.setQuery('autoplay', bcPlayerProps.autoplay === true ? 'true' : 'false');
    uri.setQuery('muted', bcPlayerProps.muted === true ? 'true' : 'false');

    if (bcPlayerProps.embedType) {
      uri.setQuery('embedType', bcPlayerProps.embedType);
    }

    if (bcPlayerProps.sizing) {
      uri.setQuery('sizing', bcPlayerProps.sizing);
    }

    return uri.toString();
  }
}
