import * as React from 'react';
import * as ReactDOMServer from 'react-dom/server';

import { Button } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCameraRetro } from '@fortawesome/free-solid-svg-icons';

import { BrightcoveApiGateway } from '@brightcove/video-cloud-sdk';

import autobind from 'autobind-decorator';
import './BrightcovePreview.css';

export class BrightcovePreview extends React.Component<BrightcovePreviewProps, BrightcovePreviewState> {
  private previewData: string | undefined;
  private previewIFrame: HTMLIFrameElement | null | undefined;

  constructor(props: BrightcovePreviewProps, context?: any) {
    super(props, context);

    this.state = {
      showCaptureOverlay: false,
      capturePosterChecked: true,
      captureThumbnailChecked: true
    };
  }

  UNSAFE_componentWillMount() {
    this.previewData = this.renderPreview();
  }

  @autobind
  cancelCapture() {
    this.setState({
      showCaptureOverlay: false,
      capturePosterChecked: true,
      captureThumbnailChecked: true,
      snapshotDataUrl: undefined,
      snapshotBlob: undefined,
      savingCaptureStatus: undefined
    });
  }

  @autobind
  async completeCapture() {
    const { accountId, videoId, onCaptureComplete, bcApiGateway } = this.props;

    const { capturePosterChecked, captureThumbnailChecked, snapshotDataUrl, snapshotBlob } = this.state;

    let posterUrl, thumbnailUrl;

    if (!bcApiGateway) {
      return;
    }

    const { dynamicIngestApi } = bcApiGateway;

    if (capturePosterChecked) {
      this.setState({
        savingCaptureStatus: 'Uploading new Poster Image...'
      });
      const uploadUrls = await dynamicIngestApi.getUploadUrls(accountId, videoId, 'CapturePoster.png');
      posterUrl = uploadUrls.api_request_url;
      await fetch(uploadUrls.signed_url, {
        method: 'PUT',
        credentials: 'omit',
        headers: {
          'Content-Type': ''
        },
        body: snapshotBlob
      });
    }

    if (captureThumbnailChecked) {
      this.setState({
        savingCaptureStatus: 'Uploading new Thumbnail...'
      });
      const uploadUrls = await dynamicIngestApi.getUploadUrls(accountId, videoId, 'CaptureThumbnail.png');
      thumbnailUrl = uploadUrls.api_request_url;
      await fetch(uploadUrls.signed_url, {
        method: 'PUT',
        credentials: 'omit',
        headers: {
          'Content-Type': ''
        },
        body: snapshotBlob
      });
    }

    if (capturePosterChecked || captureThumbnailChecked) {
      this.setState({
        savingCaptureStatus: 'Performing image ingest request...'
      });
      await dynamicIngestApi.addCustomImages(accountId, videoId, posterUrl, thumbnailUrl);

      if (onCaptureComplete && snapshotDataUrl) {
        const result: BrightcovePreviewCaptureResult = {
          snapshotDataUrl: snapshotDataUrl
        };

        if (capturePosterChecked) {
          result.posterUrl = posterUrl;
        }

        if (captureThumbnailChecked) {
          result.thumbnailUrl = thumbnailUrl;
        }

        onCaptureComplete(result);
      }
    }

    this.setState({
      showCaptureOverlay: false,
      capturePosterChecked: true,
      captureThumbnailChecked: true,
      snapshotDataUrl: undefined,
      snapshotBlob: undefined,
      savingCaptureStatus: undefined
    });
  }

  @autobind
  handleCapturePosterChanged(event: React.ChangeEvent<HTMLInputElement>) {
    const target = event.target;

    this.setState({
      capturePosterChecked: target.checked
    });
  }

  @autobind
  handleCaptureThumbnailChanged(event: React.ChangeEvent<HTMLInputElement>) {
    const target = event.target;

    this.setState({
      captureThumbnailChecked: target.checked
    });
  }

  @autobind
  async captureSnapshot() {
    if (!this.previewIFrame || !this.previewIFrame.contentDocument) {
      return;
    }

    const playerPreviewElement = this.previewIFrame.contentDocument.getElementById('bcPlayerPreview');
    if (!playerPreviewElement) {
      return;
    }

    const videoElement = playerPreviewElement.querySelector('video') as HTMLVideoElement;

    const newCanvasElement = document.createElement('canvas');

    newCanvasElement.width = videoElement.videoWidth || videoElement.clientWidth;
    newCanvasElement.height = videoElement.videoHeight || videoElement.clientHeight;

    const ctx = newCanvasElement.getContext('2d');
    if (ctx) {
      ctx.drawImage(videoElement, 0, 0, newCanvasElement.width, newCanvasElement.height);
      this.setState({
        snapshotDataUrl: newCanvasElement.toDataURL()
      });

      newCanvasElement.toBlob(blob => {
        if (!blob) {
          return;
        }

        this.setState({
          showCaptureOverlay: true,
          snapshotBlob: blob
        });
      });
    }
  }

  @autobind
  renderPreview() {
    const { accountId, videoId, playerId, style } = this.props;

    return ReactDOMServer.renderToStaticMarkup(
      <div id="bcPreview" data-vjs-player={true} style={{ width: '100%', height: (style && style.height) || 'initial' }}>
        <video
          id="bcPlayerPreview"
          // ref={el => (this.videoElement = el)}
          className="video-js"
          data-video-id={videoId}
          data-account={accountId}
          data-player={playerId || 'default'}
          data-embed="default"
          controls={true}
        />
        <script src={`//players.brightcove.net/${accountId}/${playerId || 'default'}_default/index.min.js`} />
        <style
          dangerouslySetInnerHTML={{
            __html: `
            body { margin: 0; overflow: hidden; }
          `
          }}
        />
      </div>
    );
  }

  @autobind
  renderCaptureOverlay() {
    if (!this.previewIFrame) {
      return;
    }

    const { capturePosterChecked, captureThumbnailChecked, snapshotDataUrl, savingCaptureStatus } = this.state;

    return (
      <div
        id="capture-overlay"
        className=""
        style={{ top: this.previewIFrame.offsetTop, left: this.previewIFrame.clientLeft, width: this.previewIFrame.offsetWidth, height: this.previewIFrame.offsetHeight }}
      >
        <>
          {!savingCaptureStatus && (
            <>
              <section className="headers">
                <h2>To capture a poster and/or thumbnail, select the appropriate checkbox.</h2>
                <h2>Images are not to scale.</h2>
              </section>

              <section className="previews row">
                <div className="col-md-2" />
                <div className="capture-preview col-md-4" id="poster-preview">
                  <img src={snapshotDataUrl} alt="" />
                  <div className="checkbox">
                    <input type="checkbox" checked={capturePosterChecked} onChange={this.handleCapturePosterChanged} />
                    Poster
                  </div>
                </div>
                <div className="capture-preview col-md-4" id="thumbnail-preview">
                  <img src={snapshotDataUrl} alt="" />
                  <div className="checkbox">
                    <input type="checkbox" checked={captureThumbnailChecked} onChange={this.handleCaptureThumbnailChanged} />
                    Thumbnail
                  </div>
                </div>
                <div className="col-md-2" />
              </section>

              <section className="buttons">
                <Button bsStyle="default" style={{ marginRight: '5px', width: '75px' }} onClick={this.cancelCapture}>
                  Cancel
                </Button>
                <Button bsStyle="primary" style={{ marginRight: '5px', width: '75px' }} onClick={this.completeCapture}>
                  Save
                </Button>
              </section>
            </>
          )}
          {savingCaptureStatus && <section className="headers">{savingCaptureStatus}</section>}
        </>
      </div>
    );
  }

  render() {
    const { showToolbar, style } = this.props;

    const { showCaptureOverlay } = this.state;

    return (
      <div>
        {showToolbar === undefined ||
          (showToolbar === true && (
            <div className="clearfix" style={{ background: 'black', padding: '5px' }}>
              <Button className="pull-right" bsStyle="success" onClick={this.captureSnapshot} disabled={showCaptureOverlay}>
                <FontAwesomeIcon icon={faCameraRetro} />
                &nbsp;Capture
              </Button>
            </div>
          ))}
        <iframe ref={el => (this.previewIFrame = el)} title="preview" style={{ ...style, display: 'block', border: 'none' }} sandbox="allow-same-origin allow-scripts" srcDoc={this.previewData} />
        {showCaptureOverlay === true && this.renderCaptureOverlay()}
      </div>
    );
  }
}

export interface BrightcovePreviewProps {
  accountId: string;
  videoId: string;
  playerId?: string;
  showToolbar?: boolean;
  onCaptureComplete?: (captureResult: BrightcovePreviewCaptureResult) => void;
  style?: React.CSSProperties;
  bcApiGateway?: BrightcoveApiGateway;
}

export interface BrightcovePreviewState {
  showCaptureOverlay: boolean;
  capturePosterChecked: boolean;
  captureThumbnailChecked: boolean;
  snapshotDataUrl?: string;
  snapshotBlob?: Blob;
  savingCaptureStatus?: string;
}

export interface BrightcovePreviewCaptureResult {
  snapshotDataUrl: string;
  posterUrl?: string;
  thumbnailUrl?: string;
}
