import { BrightcoveExperienceProps, BrightcoveExperienceUnits, BrightcoveExperienceOverrideMode } from '.';
import URI from 'urijs';

export class BrightcoveExperienceUtils {
  public static getDefaultBrightcoveExperienceProps(): Partial<BrightcoveExperienceProps> {
    return {
      connectorVersion: process.env.REACT_APP_VERSION,
      embedType: 'Advanced',
      sizing: 'Responsive',
      overrideWidth: true,
      playerWidth: '640',
      overrideHeight: true,
      playerHeight: '640',
      playerUnits: BrightcoveExperienceUnits.Pixels,
      overrideExperienceVideos: false,
      experienceOverrideMode: BrightcoveExperienceOverrideMode.Videos
    };
  }

  public static getBrightcoveExperiencePropsFromQueryString(): Partial<BrightcoveExperienceProps> {
    const uri = URI();
    const info = uri.query(true) as { [key: string]: string };

    const result: Partial<BrightcoveExperienceProps> = {
      accountId: info.accountId,
      experienceId: info.experienceId,
      sharepointVersion: info.SPProductNumber,
      connectorVersion: process.env.REACT_APP_VERSION,
      embedType: 'Standard',
      sizing: 'Responsive',
      overrideWidth: true,
      playerWidth: '100',
      playerUnits: BrightcoveExperienceUnits.Pct,
      overrideHeight: false,
      overrideExperienceVideos: info.overrideExperienceVideos === 'true' ? true : false
    };

    if (info.embedType === 'Standard' || info.embedType === 'Advanced') {
      result.embedType = info.embedType;
    } else {
      result.embedType = 'Advanced';
    }

    if (info.sizing === 'Fixed' || info.sizing === 'Responsive') {
      result.sizing = info.sizing;
    } else {
      result.sizing = 'Responsive';
    }

    if (info.experienceOverrideMode) {
      switch (info.experienceOverrideMode) {
        case '0':
          result.experienceOverrideMode = BrightcoveExperienceOverrideMode.Videos;
          break;
        case '1':
          result.experienceOverrideMode = BrightcoveExperienceOverrideMode.Playlist;
          break;
        default:
          result.experienceOverrideMode = BrightcoveExperienceOverrideMode.Videos;
      }
    }

    if (info.experienceOverrideModeIds) {
      result.experienceOverrideModeIds = info.experienceOverrideModeIds.split(',').map(id => id.trim());
    }

    return result;
  }

  public static augmentCurrentUriWithExperienceProps(experienceProps: Partial<BrightcoveExperienceProps>) {
    const uri = URI();

    uri.setQuery('accountId', experienceProps.accountId || '');
    uri.setQuery('experienceId', experienceProps.experienceId || '');
    uri.setQuery('overrideExperienceVideos', experienceProps.overrideExperienceVideos === true ? 'true' : 'false');
    if (experienceProps.experienceOverrideMode) {
      switch (experienceProps.experienceOverrideMode) {
        case BrightcoveExperienceOverrideMode.Videos:
          uri.setQuery('experienceOverrideMode', '0');
          break;
        case BrightcoveExperienceOverrideMode.Playlist:
          uri.setQuery('experienceOverrideMode', '1');
          break;
        default:
          uri.setQuery('experienceOverrideMode', '0');
          break;
      }
    }

    if (experienceProps.embedType) {
      uri.setQuery('embedType', experienceProps.embedType);
    }

    if (experienceProps.sizing) {
      uri.setQuery('sizing', experienceProps.sizing);
    }

    if (experienceProps.experienceOverrideModeIds) {
      uri.setQuery('experienceOverrideModeIds', experienceProps.experienceOverrideModeIds.join(','));
    }

    return uri.toString();
  }
}
